/* ===== VARIABLES FUTURISTAS OPTIMIZADAS ===== */
:root {
  /* Colores principales */
  --primary-bg: #0a0d14;
  --secondary-bg: #151922;
  --accent-bg: #1e2330;

  /* Colores de vidrio unificados */
  --glass-bg: rgb(21 25 34 / 70%);
  --glass-bg-light: rgb(255 255 255 / 5%);
  --glass-bg-medium: rgb(255 255 255 / 8%);
  --glass-bg-strong: rgb(255 255 255 / 12%);
  --glass-border: rgb(255 255 255 / 12%);

  /* Colores de marca Starflex unificados */
  --starflex-red: #FF2D55;
  --starflex-red-dark: #cc1744;
  --starflex-red-bright: #ff4569;
  --starflex-orange: #ff6b35;
  --starflex-orange-bright: #ff7a47;
  --starflex-crimson: #b8002e;
  --starflex-crimson-glow: #ff1744;
  --starflex-crimson-light: #d4003a;
  --starflex-crimson-dark: #9c0025;
  --starflex-crimson-bright: #ff4569;

  /* Efectos nen unificados */
  --neon-primary: #FF2D55;
  --neon-primary-bright: #ff4569;
  --neon-primary-glow: #ff1744;
  --neon-glow-soft: 0 0 15px rgb(255 45 85 / 40%);
  --neon-glow-medium: 0 0 25px rgb(255 45 85 / 60%);
  --neon-glow-strong: 0 0 35px rgb(255 45 85 / 80%);

  /* Sombras unificadas */
  --neon-shadow-base: 0 8px 25px rgb(255 45 85 / 30%);
  --neon-shadow-hover: 0 15px 40px rgb(255 45 85 / 50%);
  --neon-shadow-active: 0 20px 50px rgb(255 45 85 / 70%);
  --shadow-sm: 0 4px 12px rgb(0 0 0 / 15%);
  --shadow-md: 0 8px 25px rgb(0 0 0 / 20%);
  --shadow-lg: 0 15px 35px rgb(0 0 0 / 25%);
  --shadow-xl: 0 25px 50px rgb(0 0 0 / 30%);
  --shadow-2xl: 0 35px 70px rgb(0 0 0 / 40%);
  --shadow-crimson: 0 15px 40px rgb(255 45 85 / 40%);
  --shadow-crimson-hover: 0 25px 60px rgb(255 45 85 / 60%);

  /* Textos */
  --text-primary: #fff;
  --text-secondary: #e2e8f0;
  --text-muted: #94a3b8;
  --text-accent: #ff4569;

  /* Bordes unificados */
  --border-subtle: rgb(255 255 255 / 8%);
  --border-accent: rgb(255 69 105 / 30%);
  --border-crimson: rgb(184 0 46 / 40%);

  /* Efectos de brillo unificados */
  --glow-subtle: 0 0 20px rgb(255 69 105 / 20%);
  --glow-medium: 0 0 30px rgb(255 69 105 / 30%);
  --glow-strong: 0 0 40px rgb(255 69 105 / 40%);

  /* Tipografa */
  --font-primary: 'Space Grotesk', -apple-system, blinkmacsystemfont, sans-serif;
  --font-mono: 'JetBrains Mono', 'Fira Code', monospace;

  /* Espaciado fluido unificado */
  --space-xs: 0.375rem;
  --space-sm: 0.5rem;
  --space-md: 0.75rem;
  --space-lg: 1rem;
  --space-xl: 1.25rem;
  --space-2xl: 1.5rem;
  --space-3xl: 2rem;
  --space-4xl: 2.5rem;
  --space-5xl: 3rem;
  --space-6xl: 4rem;

  /* Padding fluido para secciones */
  --section-padding-x: clamp(1rem, 5vw, 8rem);
  --section-padding-y: clamp(3rem, 8vh, 8rem);

  /* NUEVAS VARIABLES PARA RESTRICCIONES DE ANCHO MXIMO */
  --max-content-width: 1400px;
  --max-hero-width: 1600px;
  --max-features-width: 1500px;
  --max-videos-width: 1200px;
  --max-faq-width: 1000px;
  --max-contact-width: 1200px;

  /* Transiciones unificadas */
  --transition: 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --transition-fast: 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --transition-slow: 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  /* Variables especficas para navbar */
  --navbar-height: 80px;
  --navbar-height-mobile: 70px;
  --navbar-z-index: 1000;
  --hamburger-size: 24px;

  /* NUEVAS VARIABLES PARA EL DRAWER MODERNO */
  --drawer-width: 70vw;
  --drawer-max-width: 350px;
  --drawer-overlay-bg: rgb(0 0 0 / 60%);
  --drawer-transition: 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  /* Variables para el selector de idioma flotante */
  --language-switcher-z-index: 1050;
  --language-switcher-safe-area: 20px;

  /* Variables mviles especficas */
  --mobile-container-padding: clamp(1rem, 4vw, 2rem);
  --mobile-section-padding: clamp(2.5rem, 8vw, 5rem);
  --mobile-text-scale: clamp(0.875rem, 3.5vw, 1rem);
  --mobile-title-scale: clamp(1.8rem, 7vw, 3.5rem);
  --mobile-button-height: clamp(48px, 12vw, 60px);
  --mobile-touch-target: 48px;

  /* Variables para tamaos de imgenes optimizadas */
  --phone-width-desktop: 280px;
  --phone-height-desktop: 560px;

  /* Variables mviles especficas */
  --phone-width-mobile: clamp(200px, 70vw, 280px);
  --phone-height-mobile: clamp(400px, 140vw, 560px);
  --phone-border-radius: clamp(30px, 6vw, 40px);

  /* Variables especficas para mvil - imgenes de caractersticas */
  --mobile-feature-image-width: clamp(180px, 50vw, 240px);
  --mobile-feature-image-height: clamp(360px, 100vw, 480px);
  --mobile-feature-image-border-radius: clamp(20px, 5vw, 30px);
}

/* ===== RESET MEJORADO ===== */
*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  position: relative;
  width: 100%;
  min-height: 100vh;
  min-height: 100dvh;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: var(--font-primary);
  line-height: 1.5;
  color: var(--text-primary);
  background: var(--primary-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ===== CONTENEDORES CON RESTRICCIONES DE ANCHO MXIMO ===== */
.hero__container,
.features__header,
.features__grid,
.videos__header,
.videos__main-container,
.videos__cta,
.faq__header,
.faq__container,
.contact__header,
.contact__grid,
.contact__footer-content {
  width: 100%;
  max-width: var(--max-content-width);
  margin-right: auto;
  margin-left: auto;
}

/* Restricciones especficas por seccin */
.hero__container {
  max-width: var(--max-hero-width);
}

.features__header,
.features__grid {
  max-width: var(--max-features-width);
}

.videos__header,
.videos__main-container,
.videos__cta {
  max-width: var(--max-videos-width);
}

.faq__header,
.faq__container {
  max-width: var(--max-faq-width);
}

.contact__header,
.contact__grid {
  max-width: var(--max-contact-width);
}

.contact__footer-content {
  max-width: var(--max-content-width);
}

/* ===== CLASES DE UTILIDAD PARA MOSTRAR/OCULTAR ===== */
.mobile-only {
  display: none;
}

.desktop-only {
  display: block;
}

@media (width <= 1023px) {
  .mobile-only {
    display: block;
  }

  .desktop-only {
    display: none;
  }
}

/* ===== OPTIMIZACIN DE IMGENES UNIFICADA - SIN HOVER ===== */
.image-optimized,
.hero__phone-app-image,
.phone__app-image {
  position: relative;
  overflow: hidden;
  image-rendering: optimize-contrast;
  image-rendering: crisp-edges;
  opacity: 1;
  transition: none;
  transform: translateZ(0);
  will-change: transform;
  backface-visibility: hidden;
  contain: layout style paint;
}

.image-optimized.loading,
.hero__phone-app-image.loading,
.phone__app-image.loading {
  background: linear-gradient(90deg,
  var(--secondary-bg) 25%,
  var(--accent-bg) 50%,
  var(--secondary-bg) 75%
);
  background-size: 200% 100%;
  animation: image-loading-shimmer 2s infinite;
}

.image-optimized.loading::after,
.hero__phone-app-image.loading::after,
.phone__app-image.loading::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  content: '';
  border: 3px solid var(--border-subtle);
  border-top: 3px solid var(--starflex-red);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: image-loading-spinner 1s linear infinite;
}

.image-optimized.loaded,
.hero__phone-app-image.loaded,
.phone__app-image.loaded {
  opacity: 1;
  transform: scale(1) translateZ(0);
}

.image-optimized.error,
.hero__phone-app-image.error,
.phone__app-image.error {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  color: var(--text-muted);
  background: var(--accent-bg);
}

.image-optimized.error::before,
.hero__phone-app-image.error::before,
.phone__app-image.error::before {
  content: ' Error cargando imagen';
}

/* Optimizacin especfica para imgenes de celular - SIN HOVER */
.hero__phone-app-image,
.phone__app-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center;
  background-size: cover;
  background-blend-mode: normal;
  isolation: isolate;
  border-radius: var(--phone-border-radius);
}

/* Configuracin de imgenes por data-feature */
.hero__phone-app-image {
  background-image: url('../assets/phones/Hero.avif');
  filter: brightness(1.04) contrast(1.12) saturate(1.15) drop-shadow(0 0 1px rgb(0 0 0 / 10%));
}

.feature[data-feature="schedule"] .phone__app-image {
  background-image: url('../assets/phones/Horario.avif');
}

.feature[data-feature="stations"] .phone__app-image {
  background-image: url('../assets/phones/Estaciones.avif');
}

.feature[data-feature="calendar"] .phone__app-image {
  background-image: url('../assets/phones/Calendario.avif');
}

.feature[data-feature="log"] .phone__app-image {
  background-image: url('../assets/phones/Registro.avif');
}

.feature[data-feature="notifications"] .phone__app-image {
  background-image: url('../assets/phones/Notificaciones.avif');
}

.feature[data-feature="referrals"] .phone__app-image {
  background-image: url('../assets/phones/Referidos.avif');
}

/* ===== PREVENCIN DE SCROLL CUANDO EL MEN EST ABIERTO ===== */
body.no-scroll {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* ===== HEADER FUTURISTA FLOTANTE ===== */
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--navbar-z-index);
  width: 100%;
  height: var(--navbar-height);
  padding: 0;
  overflow: visible;
  pointer-events: none;
  background: transparent;
  transition: all var(--transition-slow);
  will-change: transform;
}

/* ===== NAVBAR FUTURISTA ULTRA RESPONSIVE ===== */
.nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: var(--max-content-width);
  height: 100%;
  padding: 0 var(--section-padding-x);
  margin: 0 auto;
  overflow: visible;
  pointer-events: none;
}

/* ===== LOGO FLOTANTE OPTIMIZADO - SIN HOVER ===== */
.nav__brand {
  z-index: calc(var(--navbar-z-index) + 1);
  pointer-events: auto;
}

.nav__logo {
  position: relative;
  width: 55px;
  height: 55px;
  cursor: pointer;
  image-rendering: optimize-contrast;
  image-rendering: crisp-edges;
  background-image: url('../assets/logo.avif');
  filter: drop-shadow(0 8px 25px rgb(0 0 0 / 30%));
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 12px;
  transition: none;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.nav__logo:focus {
  border-radius: 15px;
  outline: 2px solid var(--starflex-red);
  outline-offset: 3px;
}

/* ===== NAVEGACIN CENTRADA DESKTOP ===== */
.nav__menu {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  overflow: hidden;
  pointer-events: auto;
  transition: all var(--transition);
  transform: translate(-50%, -50%);
}

.nav__list {
  position: relative;
  display: flex;
  gap: var(--space-2xl);
  align-items: center;
  width: auto;
  padding: var(--space-md) var(--space-3xl);
  overflow: hidden;
  white-space: nowrap;
  list-style: none;
  background: rgb(15 20 30 / 90%);
  backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  border: 1px solid var(--glass-border);
  border-radius: 50px;
  box-shadow:
    0 8px 32px rgb(0 0 0 / 40%),
    0 0 0 1px rgb(255 69 105 / 10%),
    inset 0 1px 0 rgb(255 255 255 / 8%);
}

.nav__list::before {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(255 69 105 / 8%),
  transparent
);
  transition: left 2s ease;
}

.nav__list:hover::before {
  left: 100%;
}

/* ===== ENLACES FUTURISTAS OPTIMIZADOS ===== */
.nav__link {
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  padding: var(--space-sm) var(--space-md);
  overflow: hidden;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  white-space: nowrap;
  border-radius: 25px;
  transition: all var(--transition);
}

.nav__link::before {
  position: absolute;
  inset: 0;
  z-index: -1;
  content: '';
  background: var(--glass-bg-light);
  border-radius: 25px;
  opacity: 0;
  transition: all var(--transition);
}

.nav__link::after {
  position: absolute;
  bottom: 3px;
  left: 50%;
  width: 0;
  height: 2px;
  content: '';
  background: linear-gradient(90deg,
  var(--starflex-crimson-glow) 0%,
  var(--starflex-crimson-bright) 50%,
  var(--starflex-crimson-glow) 100%
);
  border-radius: 1px;
  box-shadow:
    0 0 10px var(--starflex-crimson-glow),
    0 0 20px var(--starflex-crimson-bright);
  opacity: 0;
  transition: all var(--transition-slow);
  transform: translateX(-50%);
}

.nav__link:hover {
  color: var(--text-primary);
  text-shadow: 0 0 15px rgb(255 255 255 / 60%);
  transform: translateY(-2px);
}

.nav__link:hover::before {
  opacity: 1;
}

.nav__link:hover::after {
  width: 85%;
  opacity: 1;
}

.nav__link.active {
  color: var(--starflex-crimson-bright);
  text-shadow: 0 0 20px var(--starflex-crimson-glow);
}

.nav__link.active::after {
  width: 85%;
  opacity: 1;
}

.nav__link:focus {
  border-radius: 28px;
  outline: 2px solid var(--starflex-red);
  outline-offset: 3px;
}

/* ===== BOTN CTA FLOTANTE ===== */
.nav__cta {
  z-index: calc(var(--navbar-z-index) + 1);
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  align-items: flex-end;
  padding: var(--space-md);
  margin: var(--space-md);
  pointer-events: auto;
}

/* ===== TOGGLE MVIL HAMBURGUESA ===== */
.nav__toggle {
  position: relative;
  z-index: calc(var(--navbar-z-index) + 2);
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  overflow: hidden;
  pointer-events: auto;
  cursor: pointer;
  background: rgb(15 20 30 / 95%);
  backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 15px;
  box-shadow:
    0 8px 25px rgb(0 0 0 / 30%),
    0 0 20px rgb(255 69 105 / 20%);
  transition: all var(--transition-fast);
}

.nav__toggle::before {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(255 69 105 / 10%),
  transparent
);
  transition: left 1.2s ease;
}

.nav__toggle:hover {
  border-color: var(--border-accent);
  box-shadow:
    0 10px 30px rgb(0 0 0 / 40%),
    0 0 30px rgb(255 69 105 / 30%);
  transform: translateY(-2px);
}

.nav__toggle:hover::before {
  left: 100%;
}

.nav__toggle:focus {
  border-radius: 18px;
  outline: 2px solid var(--starflex-red);
  outline-offset: 3px;
}

.nav__toggle:active {
  transform: translateY(0) scale(0.95);
}

/* ===== LNEAS DEL HAMBURGUESA ===== */
.nav__toggle-line {
  position: relative;
  width: var(--hamburger-size);
  height: 2px;
  background: var(--text-primary);
  border-radius: 2px;
  transition: all var(--transition-fast);
  transform-origin: center;
}

.nav__toggle-line:not(:last-child) {
  margin-bottom: 5px;
}

/* Estados activos del hamburguesa */
.nav__toggle.active .nav__toggle-line:nth-child(1) {
  background: var(--starflex-crimson-bright);
  box-shadow: 0 0 10px var(--starflex-crimson-glow);
  transform: rotate(45deg) translate(6px, 6px);
}

.nav__toggle.active .nav__toggle-line:nth-child(2) {
  opacity: 0;
  transform: scale(0);
}

.nav__toggle.active .nav__toggle-line:nth-child(3) {
  background: var(--starflex-crimson-bright);
  box-shadow: 0 0 10px var(--starflex-crimson-glow);
  transform: rotate(-45deg) translate(6px, -6px);
}

.nav__toggle.active {
  background: rgb(15 20 30 / 98%);
  border-color: var(--starflex-crimson-bright);
  box-shadow:
    0 10px 30px rgb(0 0 0 / 50%),
    0 0 30px var(--starflex-crimson-glow);
}

/* ===== SELECTOR DE IDIOMA FLOTANTE ===== */
.language-switcher {
  position: fixed;
  top: 100px;
  right: 20px;
  z-index: var(--language-switcher-z-index);
  pointer-events: auto;
}

.language-switcher__btn {
  position: relative;
  display: flex;
  gap: var(--space-sm);
  align-items: center;
  min-width: 100px;
  padding: var(--space-sm) var(--space-lg);
  overflow: hidden;
  font-family: var(--font-mono);
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-primary);
  cursor: pointer;
  background: linear-gradient(135deg,
  rgb(21 25 34 / 95%) 0%,
  rgb(30 35 48 / 95%) 100%
);
  backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  border: 1px solid var(--border-accent);
  border-radius: 25px;
  box-shadow:
    var(--shadow-md),
    var(--glow-subtle),
    inset 0 1px 0 rgb(255 255 255 / 8%);
  transition: all var(--transition);
}

.language-switcher__btn::before {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(255 69 105 / 10%),
  transparent
);
  transition: left 1.2s ease;
}

.language-switcher__btn:hover {
  border-color: var(--starflex-crimson-bright);
  box-shadow:
    var(--shadow-lg),
    var(--glow-medium),
    inset 0 1px 0 rgb(255 255 255 / 12%);
  transform: translateY(-2px) scale(1.02);
}

.language-switcher__btn:hover::before {
  left: 100%;
}

.language-switcher__btn:focus {
  border-radius: 28px;
  outline: 2px solid var(--starflex-crimson-bright);
  outline-offset: 3px;
}

.language-switcher__icon {
  width: 16px;
  height: 16px;
  color: var(--starflex-crimson-bright);
  filter: drop-shadow(0 0 8px rgb(255 69 105 / 40%));
  transition: all var(--transition);
}

.language-switcher__btn:hover .language-switcher__icon {
  color: var(--starflex-crimson-glow);
  filter: drop-shadow(0 0 12px rgb(255 69 105 / 60%));
  transform: scale(1.1);
}

.language-switcher__text {
  font-weight: 700;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all var(--transition);
}

.language-switcher__btn:hover .language-switcher__text {
  color: var(--starflex-crimson-bright);
  text-shadow: 0 0 10px rgb(255 69 105 / 50%);
}

.language-switcher__arrow {
  width: 14px;
  height: 14px;
  color: var(--text-muted);
  transition: all var(--transition);
  transform-origin: center;
}

.language-switcher__btn:hover .language-switcher__arrow {
  color: var(--text-primary);
  transform: rotate(180deg);
}

.language-switcher.active .language-switcher__arrow {
  color: var(--starflex-crimson-bright);
  transform: rotate(180deg);
}

/* ===== DROPDOWN DEL SELECTOR DE IDIOMA ===== */
.language-switcher__dropdown {
  position: absolute;
  top: calc(100% + 8px);
  right: 0;
  z-index: 10;
  min-width: 160px;
  padding: var(--space-sm);
  visibility: hidden;
  background: linear-gradient(135deg,
  rgb(21 25 34 / 98%) 0%,
  rgb(30 35 48 / 98%) 100%
);
  backdrop-filter: blur(30px);
  backdrop-filter: blur(30px);
  border: 1px solid var(--border-accent);
  border-radius: 15px;
  box-shadow:
    var(--shadow-xl),
    var(--glow-medium),
    inset 0 1px 0 rgb(255 255 255 / 8%);
  opacity: 0;
  transition: all var(--transition);
  transform: translateY(-10px) scale(0.95);
}

.language-switcher.active .language-switcher__dropdown {
  visibility: visible;
  opacity: 1;
  transform: translateY(0) scale(1);
}

.language-switcher__option {
  position: relative;
  display: flex;
  gap: var(--space-sm);
  align-items: center;
  width: 100%;
  padding: var(--space-sm) var(--space-md);
  overflow: hidden;
  font-family: var(--font-primary);
  font-size: 0.85rem;
  font-weight: 500;
  color: var(--text-secondary);
  cursor: pointer;
  background: transparent;
  border: none;
  border-radius: 12px;
  transition: all var(--transition);
}

.language-switcher__option::before {
  position: absolute;
  inset: 0;
  content: '';
  background: linear-gradient(135deg,
  rgb(255 69 105 / 8%) 0%,
  rgb(255 23 68 / 5%) 100%
);
  border-radius: 12px;
  opacity: 0;
  transition: all var(--transition);
}

.language-switcher__option:hover {
  color: var(--text-primary);
  transform: translateX(3px);
}

.language-switcher__option:hover::before {
  opacity: 1;
}

.language-switcher__option.active {
  color: var(--starflex-crimson-bright);
  background: rgb(255 69 105 / 10%);
  border: 1px solid rgb(255 69 105 / 20%);
}

.language-switcher__option.active::before {
  background: linear-gradient(135deg,
  rgb(255 69 105 / 15%) 0%,
  rgb(255 23 68 / 10%) 100%
);
  opacity: 1;
}

.language-switcher__flag {
  font-size: 1rem;
  line-height: 1;
}

.language-switcher__name {
  flex: 1;
  font-weight: 600;
  text-align: left;
}

/* ===== BOTONES PRINCIPALES CON EFECTOS NEN UNIFICADOS ===== */
.btn {
  position: relative;
  display: inline-flex;
  gap: var(--space-xs);
  align-items: center;
  justify-content: center;
  padding: var(--space-md) var(--space-xl);
  overflow: hidden;
  font-family: var(--font-primary);
  font-size: 0.85rem;
  font-weight: 600;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  transition: all var(--transition);
}

/* ===== BOTONES PRIMARIOS CON EFECTOS NEN ===== */
.btn--primary,
.btn--cta,
.btn--cta-video,
.btn--footer {
  position: relative;
  overflow: hidden;
  font-weight: 700 !important;
  color: #fff !important;
  text-shadow:
    0 0 10px rgb(255 255 255 / 80%),
    0 0 20px rgb(255 45 85 / 60%) !important;
  text-transform: uppercase !important;
  letter-spacing: 1px !important;
  background: linear-gradient(135deg,
  var(--starflex-crimson) 0%,
  var(--neon-primary) 50%,
  var(--neon-primary-bright) 100%
) !important;
  border: 2px solid rgb(255 45 85 / 50%) !important;
  box-shadow:
    var(--neon-shadow-base),
    var(--neon-glow-soft),
    inset 0 1px 0 rgb(255 255 255 / 20%) !important;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  transform: translateZ(0) !important;
  will-change: transform, box-shadow !important;
  backface-visibility: hidden !important;
}

/* ===== EFECTO DE BRILLO INTERNO ANIMADO ===== */
.btn--primary::before,
.btn--cta::before,
.btn--cta-video::before,
.btn--footer::before {
  position: absolute !important;
  top: 0 !important;
  left: -100% !important;
  z-index: 1 !important;
  width: 100% !important;
  height: 100% !important;
  content: '' !important;
  background: linear-gradient(90deg,
  transparent,
  rgb(255 255 255 / 40%),
  transparent
) !important;
  border-radius: inherit !important;
  transition: left 1s ease !important;
}

/* ===== ANILLO EXTERIOR NEN ===== */
.btn--primary::after,
.btn--cta::after,
.btn--cta-video::after,
.btn--footer::after {
  position: absolute !important;
  inset: -3px !important;
  z-index: -1 !important;
  content: '' !important;
  background: linear-gradient(45deg,
  var(--neon-primary-glow),
  var(--neon-primary-bright),
  var(--neon-primary-glow),
  var(--neon-primary-bright)
) !important;
  filter: blur(2px) !important;
  border-radius: inherit !important;
  opacity: 0 !important;
  transition: opacity 0.3s ease !important;
}

/* ===== ESTADOS HOVER MEJORADOS ===== */
.btn--primary:hover,
.btn--cta:hover,
.btn--cta-video:hover,
.btn--footer:hover {
  text-shadow:
    0 0 15px rgb(255 255 255 / 100%),
    0 0 25px rgb(255 45 85 / 80%),
    0 0 35px rgb(255 45 85 / 60%) !important;
  border: 2px solid rgb(255 45 85 / 80%) !important;
  box-shadow:
    var(--neon-shadow-hover),
    var(--neon-glow-medium),
    var(--neon-glow-strong),
    inset 0 1px 0 rgb(255 255 255 / 30%) !important;
  transform: translateY(-3px) scale(1.03) translateZ(0) !important;
}

/* Activar efectos en hover */
.btn--primary:hover::before,
.btn--cta:hover::before,
.btn--cta-video:hover::before,
.btn--footer:hover::before {
  left: 100% !important;
}

.btn--primary:hover::after,
.btn--cta:hover::after,
.btn--cta-video:hover::after,
.btn--footer:hover::after {
  opacity: 1 !important;
}

/* ===== ESTADOS ACTIVOS/PRESIONADOS ===== */
.btn--primary:active,
.btn--cta:active,
.btn--cta-video:active,
.btn--footer:active {
  box-shadow:
    var(--neon-shadow-active),
    var(--neon-glow-strong),
    inset 0 1px 0 rgb(255 255 255 / 40%) !important;
  transform: translateY(-1px) scale(1.01) translateZ(0) !important;
}

/* ===== ESTADOS FOCUS PARA ACCESIBILIDAD ===== */
.btn--primary:focus,
.btn--cta:focus,
.btn--cta-video:focus,
.btn--footer:focus {
  border-radius: inherit !important;
  outline: 3px solid rgb(255 45 85 / 50%) !important;
  outline-offset: 3px !important;
}

/* ===== CORRECCIN ESPECFICA PARA TEXTO INTERNO ===== */
.btn--primary span,
.btn--cta span,
.btn--cta-video span,
.btn--footer span {
  position: relative !important;
  z-index: 2 !important;
  font-weight: 800 !important;
  letter-spacing: 1px !important;
  background: linear-gradient(135deg,
  #fff 0%,
  #ffebee 50%,
  #fff 100%
) !important;
  background-clip: text !important;
  background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
}

.btn--primary small,
.btn--cta small,
.btn--cta-video small,
.btn--footer small {
  position: relative !important;
  z-index: 2 !important;
  font-family: var(--font-mono) !important;
  font-weight: 600 !important;
  color: rgb(255 255 255 / 95%) !important;
  text-shadow: 0 0 10px rgb(255 45 85 / 70%) !important;
  text-transform: uppercase !important;
  letter-spacing: 2px !important;
}

.btn--secondary {
  padding: var(--space-lg) var(--space-2xl);
  font-size: 1rem;
  font-weight: 600;
  color: var(--starflex-red);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  background: transparent;
  border: 2px solid var(--starflex-red);
  border-radius: 16px;
  transition: all var(--transition);
}

.btn--secondary:hover {
  color: white;
  background: var(--starflex-red);
  box-shadow:
    var(--shadow-lg),
    0 0 25px rgb(255 69 105 / 40%);
  transform: translateY(-2px);
}

/* ===== FONDO FUTURISTA AVANZADO ===== */
.bg-canvas {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.bg-canvas::before {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  content: '';
  background:
    radial-gradient(circle at 20% 80%, rgb(255 69 105 / 6%) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgb(139 92 246 / 4%) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgb(255 69 105 / 3%) 0%, transparent 50%),
    radial-gradient(circle at 60% 80%, rgb(255 107 53 / 2%) 0%, transparent 50%);
  animation: aurora-flow 60s ease-in-out infinite;
}

.bg-canvas::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-image:
    linear-gradient(45deg, transparent 24%, rgb(255 69 105 / 1.5%) 25%, rgb(255 69 105 / 1.5%) 26%, transparent 27%),
    linear-gradient(-45deg, transparent 24%, rgb(139 92 246 / 1%) 25%, rgb(139 92 246 / 1%) 26%, transparent 27%);
  background-size: 60px 60px, 80px 80px;
  opacity: 0.4;
  animation: grid-drift 180s linear infinite;
}

/* ===== HERO SECTION MEJORADO ===== */
.hero {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: calc(var(--navbar-height) + var(--space-xl)) var(--section-padding-x) var(--section-padding-y);
  overflow: hidden;
  background:
linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
}

.hero::before {
  position: absolute;
  inset: 0;
  z-index: 1;
  content: '';
  background:
    radial-gradient(ellipse at top center, rgb(255 69 105 / 8%) 0%, transparent 60%),
    radial-gradient(ellipse at bottom left, rgb(139 92 246 / 6%) 0%, transparent 50%),
    radial-gradient(ellipse at bottom right, rgb(255 23 68 / 4%) 0%, transparent 50%);
  animation: hero-glow 30s ease-in-out infinite;
}

.hero::after {
  position: absolute;
  top: 20%;
  left: 10%;
  z-index: 1;
  width: 300px;
  height: 300px;
  content: '';
  background: radial-gradient(circle, rgb(255 69 105 / 10%) 0%, transparent 70%);
  filter: blur(40px);
  border-radius: 50%;
  animation: floating-orb-1 20s ease-in-out infinite;
}

/* Orbes flotantes adicionales */
.hero .floating-orb-2 {
  position: absolute;
  top: 60%;
  right: 15%;
  z-index: 1;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgb(139 92 246 / 8%) 0%, transparent 70%);
  filter: blur(30px);
  border-radius: 50%;
  animation: floating-orb-2 25s ease-in-out infinite;
}

.hero .floating-orb-3 {
  position: absolute;
  bottom: 20%;
  left: 20%;
  z-index: 1;
  width: 150px;
  height: 150px;
  background: radial-gradient(circle, rgb(255 69 105 / 6%) 0%, transparent 70%);
  filter: blur(25px);
  border-radius: 50%;
  animation: floating-orb-3 18s ease-in-out infinite;
}

.hero__container {
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: var(--space-5xl);
  align-items: center;
  width: 100%;
}

.hero__content {
  width: 100%;
  animation: fadeInUp 1s ease-out;
}

/* ===== BOTONES DE ETIQUETAS NO CLICKEABLES ===== */
.hero__badge,
.videos__badge {
  position: relative;
  display: inline-flex;
  gap: var(--space-sm);
  align-items: center;
  padding: 6px 16px;
  margin-bottom: var(--space-xl);
  overflow: hidden;
  font-family: Sora, sans-serif;
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--starflex-red);
  text-transform: uppercase;
  letter-spacing: 0.8px;
  pointer-events: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid var(--starflex-red);
  border-radius: 999px;
  transition: all var(--transition);
}

.hero__badge::before,
.videos__badge::before {
  width: 8px;
  height: 8px;
  content: '';
  background: var(--starflex-crimson-bright);
  border-radius: 50%;
  box-shadow: var(--glow-subtle);
  animation: pulse-gentle 4s infinite;
}

.videos__badge-icon {
  font-size: 1rem;
  color: var(--starflex-crimson-bright);
  text-shadow: 0 0 10px var(--starflex-crimson-glow);
  animation: pulse-gentle 4s infinite;
}

/* ===== TTULO HERO ===== */
.hero__title {
  position: relative;
  margin-bottom: var(--space-xl);
  font-size: clamp(2.8rem, 8vw, 5.5rem);
  font-weight: 900;
  line-height: 0.9;
  text-align: left;
  letter-spacing: -0.03em;
}

.hero__title--main {
  display: block;
  margin-bottom: var(--space-sm);
  font-weight: 900;
  text-shadow: 0 0 30px rgb(255 255 255 / 10%);
  background: linear-gradient(135deg,
  #fff 0%,
  #f8fafc 30%,
  #e2e8f0 70%,
  #cbd5e1 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero__title--highlight {
  position: relative;
  display: block;
  margin-bottom: var(--space-sm);
  font-weight: 900;
  text-shadow: var(--glow-medium);
  background: linear-gradient(135deg,
  var(--starflex-crimson) 0%,
  var(--starflex-crimson-bright) 30%,
  var(--starflex-crimson-glow) 70%,
  #ff6b9d 100%
);
  filter: drop-shadow(0 0 20px rgb(255 69 105 / 40%));
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero__title--highlight::after {
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 4px;
  content: '';
  background: linear-gradient(90deg,
  var(--starflex-crimson) 0%,
  var(--starflex-crimson-bright) 50%,
  transparent 100%
);
  border-radius: 2px;
  opacity: 0.8;
  animation: glow-line 6s ease-in-out infinite;
}

.hero__title--amazon {
  position: relative;
  display: block;
  font-family: var(--font-primary);
  font-weight: 900;
  text-shadow: 0 0 25px rgb(255 107 53 / 40%);
  background: linear-gradient(135deg,
  var(--starflex-orange) 0%,
  var(--starflex-orange-bright) 30%,
  #ff8c42 70%,
  #ff4500 100%
);
  filter: drop-shadow(0 0 15px rgb(255 107 53 / 30%));
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* ===== DESCRIPCIN MEJORADA ===== */
.hero__company-description {
  position: relative;
  max-width: 650px;
  padding: var(--space-xl);
  margin-bottom: var(--space-xl);
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.7;
  color: var(--text-secondary);
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-subtle);
  border-left: 4px solid var(--starflex-crimson-bright);
  border-radius: 20px;
  box-shadow: var(--shadow-md);
}

.hero__company-description::before {
  position: absolute;
  top: var(--space-lg);
  left: var(--space-lg);
  width: 12px;
  height: 12px;
  content: '';
  background: var(--starflex-crimson-bright);
  border-radius: 50%;
  box-shadow: var(--glow-subtle);
}

.hero__company-description-text {
  margin-left: var(--space-xl);
  font-style: italic;
  font-weight: 300;
}

.hero__subtitle {
  position: relative;
  max-width: 600px;
  margin-bottom: var(--space-2xl);
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.7;
  color: var(--text-secondary);
}

.hero__subtitle::before {
  margin-right: var(--space-md);
  font-family: var(--font-mono);
  font-weight: 600;
  color: var(--starflex-crimson-bright);
  content: '//';
  opacity: 0.7;
}

.hero__cta-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
  margin-bottom: var(--space-2xl);
}

.hero__cta-main {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
  align-items: center;
}

.hero__cta-main small {
  font-family: var(--font-mono);
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: 0.95;
}

/* ===== BOTN CTA PRINCIPAL ===== */
.btn--cta {
  position: relative;
  min-width: 220px;
  padding: var(--space-lg) var(--space-2xl);
  overflow: hidden;
  font-size: 1rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 0 15px rgb(255 255 255 / 60%);
  background: linear-gradient(135deg,
  var(--starflex-crimson-dark) 0%,
  var(--starflex-crimson) 25%,
  var(--starflex-crimson-light) 50%,
  var(--starflex-crimson-glow) 75%,
  var(--starflex-crimson-bright) 100%
);
  border: 2px solid rgb(255 69 105 / 40%);
  border-radius: 16px;
  box-shadow:
    var(--shadow-crimson),
    var(--glow-medium),
    inset 0 1px 0 rgb(255 255 255 / 15%);
  transition: all var(--transition);
  transform: translateZ(0);
  will-change: transform, box-shadow;
  backface-visibility: hidden;
}

.btn--cta::before {
  position: absolute;
  top: 0;
  left: -100%;
  z-index: 1;
  width: 100%;
  height: 100%;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(255 255 255 / 30%),
  transparent
);
  transition: left 1.2s ease;
}

.btn--cta:hover {
  text-shadow: 0 0 20px rgb(255 255 255 / 90%);
  border-color: rgb(255 69 105 / 70%);
  box-shadow:
    var(--shadow-crimson-hover),
    var(--glow-strong),
    inset 0 1px 0 rgb(255 255 255 / 25%);
  transform: translateY(-4px) scale(1.05);
}

.btn--cta:hover::before {
  left: 100%;
}

.btn--cta .hero__cta-main span {
  position: relative;
  z-index: 2;
  font-weight: 800;
  letter-spacing: 1px;
  background: linear-gradient(135deg,
  #fff 0%,
  #ffebee 50%,
  #fff 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.btn--cta .hero__cta-main small {
  position: relative;
  z-index: 2;
  font-family: var(--font-mono);
  font-weight: 600;
  color: rgb(255 255 255 / 95%);
  text-shadow: 0 0 10px rgb(255 69 105 / 70%);
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* ===== BOTONES DE DESCARGA OPTIMIZADOS - SIN HOVER ===== */
.hero__download {
  display: flex;
  gap: var(--space-xl);
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  margin-bottom: var(--space-lg);
}

.download-btn {
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  overflow: visible;
  text-decoration: none;
  cursor: pointer;
  filter: drop-shadow(0 8px 25px rgb(0 0 0 / 30%));
  transition: none;
}

.download-btn__image {
  display: block;
  width: auto;
  max-width: 200px;
  height: 60px;
  object-fit: contain;
  object-position: center;
  image-rendering: optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: high-quality;
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  transition: none;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.download-btn--google .download-btn__image {
  width: auto;
  min-width: 180px;
  max-width: 200px;
  height: 60px;
  background-image: url('../assets/GooglePlay.avif');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.download-btn--app-store .download-btn__image {
  width: auto;
  min-width: 180px;
  max-width: 200px;
  height: 60px;
  background-image: url('../assets/AppleStore.avif');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.hero__trust {
  display: flex;
  gap: var(--space-sm);
  align-items: center;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-muted);
}

.hero__trust::before {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: var(--space-sm);
  content: '';
  background: var(--starflex-crimson-glow);
  border-radius: 50%;
  box-shadow: var(--glow-subtle);
  animation: pulse-gentle 4s infinite;
}

/* ===== VIDEO MVIL DEBAJO DEL TEXTO DE CONFIANZA ===== */
.hero__mobile-video {
  width: 100%;
  max-width: 320px;
  margin: var(--space-xl) auto 0;
  overflow: hidden;
  filter: drop-shadow(0 0 20px rgb(255 69 105 / 15%));
  border-radius: 20px;
  box-shadow:
    0 20px 60px rgb(0 0 0 / 40%),
    0 8px 30px rgb(0 0 0 / 30%),
    0 0 0 1px rgb(255 255 255 / 5%);
}

.hero__mobile-video-player {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  border-radius: 20px;
}

.hero__mobile-video-player::-webkit-media-controls {
  display: none !important;
}

/* ===== TELFONO HERO OPTIMIZADO - SOLO DESKTOP - SIN HOVER ===== */
.hero__media {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  animation: fadeInRight 1s ease-out 0.3s both;
}

.hero__phone-container {
  position: relative;

  /* HACER TRANSPARENTE EL CONTENEDOR DEL TELFONO */
  background: transparent !important;
  backdrop-filter: none !important;
  backdrop-filter: none !important;
  transition: none;
  transform: perspective(1200px) rotateY(-8deg) rotateX(2deg);
}

.hero__phone {
  position: relative;
  width: var(--phone-width-desktop);
  height: var(--phone-height-desktop);
  overflow: hidden;

  /* HACER TRANSPARENTE EL TELFONO */
  background: transparent !important;
  filter: none !important;
  border: none !important;
  border-radius: var(--phone-border-radius);
  box-shadow: none !important;
  will-change: transform;
  contain: layout style paint;
}

.hero__phone::before {
  /* ELIMINAR EL EFECTO DE BRILLO AMBIENTAL */
  display: none !important;
}

.hero__phone-screen {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: transparent !important;
  border: none !important;
  border-radius: var(--phone-border-radius);
}

.hero__phone-content {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: transparent !important;
  border-radius: var(--phone-border-radius);
}

/* ===== VIDEO DEL HERO OPTIMIZADO ===== */
.hero__phone-video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  image-rendering: optimize-contrast;
  image-rendering: crisp-edges;
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  border-radius: var(--phone-border-radius);
  opacity: 1;
  transition: none;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.hero__phone-video::-webkit-media-controls {
  display: none !important;
}

/* Estados de carga para video */
.hero__phone-video.loading {
  filter: brightness(0.8) contrast(0.9);
  opacity: 0.7;
}

.hero__phone-video.loaded {
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  opacity: 1;
}

.hero__phone-video.error {
  display: none;
}

/* Mostrar imagen de fallback cuando el video falla */
.hero__phone-video.error + .hero__phone-app-image {
  z-index: 2;
  display: block;
}

/* ===== SECCIN CARACTERSTICAS OPTIMIZADA - SIN HOVER ===== */
.features {
  position: relative;
  width: 100%;
  padding: var(--section-padding-y) var(--section-padding-x);
  overflow: hidden;
  background:
linear-gradient(180deg, var(--primary-bg) 0%, var(--secondary-bg) 50%, var(--primary-bg) 100%);
}

.features::before {
  position: absolute;
  inset: 0;
  z-index: 1;
  content: '';
  background:
    radial-gradient(ellipse at 25% 25%, rgb(255 69 105 / 4%) 0%, transparent 60%),
    radial-gradient(ellipse at 75% 75%, rgb(184 0 46 / 3%) 0%, transparent 60%);
}

.features__header {
  position: relative;
  z-index: 3;
  margin-bottom: var(--space-5xl);
  text-align: center;
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 1s ease-out 0.2s forwards;
}

.features__title {
  position: relative;
  margin-bottom: var(--space-xl);
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 800;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg,
  var(--text-primary) 0%,
  var(--starflex-red) 50%,
  var(--starflex-crimson-glow) 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.features__title::after {
  position: absolute;
  bottom: -8px;
  left: 50%;
  width: 60px;
  height: 3px;
  content: '';
  background: linear-gradient(90deg,
  var(--starflex-red),
  var(--starflex-crimson-glow)
);
  border-radius: 2px;
  transform: translateX(-50%);
}

.features__subtitle {
  max-width: 700px;
  margin: 0 auto;
  font-size: 1.3rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.features__grid {
  position: relative;
  z-index: 3;
  display: grid;
  gap: var(--space-6xl);
  width: 100%;
}

/* ===== CARACTERSTICAS INDIVIDUALES ===== */
.feature {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-5xl);
  align-items: center;
  width: 100%;
  opacity: 0;
  transform: translateY(50px);
}

.feature.in-view {
  animation: slideInUp 1s ease-out forwards;
}

.feature:nth-child(even) {
  direction: rtl;
}

.feature:nth-child(even) > * {
  direction: ltr;
}

/* ===== CONTENIDO DE CARACTERSTICAS OPTIMIZADO ===== */
.feature__content {
  position: relative;
  width: 100%;
  padding: 0;
}

.feature__title {
  margin-bottom: var(--space-xl);
  font-family: var(--font-mono);
  font-size: clamp(2.2rem, 5.5vw, 2.8rem);
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg,
  var(--text-primary) 0%,
  var(--starflex-red) 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.feature__description {
  max-width: none;
  margin-bottom: var(--space-xl);
  font-size: 1.2rem;
  line-height: 1.7;
  color: var(--text-secondary);
}

.feature__list {
  padding: 0;
  margin-bottom: var(--space-2xl);
  list-style: none;
}

.feature__list-item {
  display: flex;
  gap: var(--space-md);
  align-items: flex-start;
  padding: var(--space-sm) 0;
  padding-left: 0;
  margin-bottom: var(--space-lg);
  font-size: 1.1rem;
  line-height: 1.6;
  color: var(--text-secondary);
  text-align: left;
  border-radius: 8px;
  transition: none;
}

.feature__list-item::before {
  flex-shrink: 0;
  width: 8px;
  height: 8px;
  margin-top: 8px;
  content: '';
  background: var(--starflex-crimson-glow);
  border-radius: 50%;
  box-shadow: 0 0 10px var(--starflex-crimson-glow);
  animation: pulse-gentle 4s infinite;
}

/* ===== TELFONOS DE CARACTERSTICAS - DESKTOP Y MVIL - SIN HOVER ===== */
.feature__phone {
  position: relative;
  width: var(--phone-width-desktop);
  height: var(--phone-height-desktop);
  margin: 0 auto;
  filter: drop-shadow(0 10px 30px rgb(255 69 105 / 15%));
  transition: none;
  transform: perspective(1000px) rotateY(2deg) rotateX(-1deg);
}

.feature__phone::before {
  position: absolute;
  inset: -2px;
  z-index: -1;
  content: '';
  background: linear-gradient(45deg,
  rgb(255 69 105 / 10%) 0%,
  rgb(255 23 68 / 5%) 25%,
  rgb(255 69 105 / 10%) 50%,
  rgb(255 23 68 / 5%) 75%,
  rgb(255 69 105 / 10%) 100%
);
  border-radius: calc(var(--phone-border-radius) + 2px);
  opacity: 0;
  transition: opacity var(--transition);
}

.feature.in-view .feature__phone::before {
  opacity: 1;
}

.phone {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #000;
  background: linear-gradient(135deg,
  rgb(255 69 105 / 2%) 0%,
  rgb(0 0 0 / 95%) 20%,
  rgb(0 0 0 / 100%) 80%,
  rgb(255 69 105 / 2%) 100%
);
  border-radius: var(--phone-border-radius);
  box-shadow:
    0 20px 60px rgb(0 0 0 / 40%),
    0 8px 30px rgb(0 0 0 / 30%),
    0 0 0 1px rgb(255 255 255 / 5%);
  transition: none;
  will-change: transform;
  contain: layout style paint;
}

.feature[data-feature="schedule"] .phone {
  filter: drop-shadow(0 0 25px rgb(255 69 105 / 12%));
}

.feature[data-feature="stations"] .phone {
  filter: drop-shadow(0 0 25px rgb(255 23 68 / 12%));
}

.feature[data-feature="calendar"] .phone {
  filter: drop-shadow(0 0 25px rgb(255 45 107 / 12%));
}

.feature[data-feature="log"] .phone {
  filter: drop-shadow(0 0 25px rgb(184 0 46 / 12%));
}

.feature[data-feature="notifications"] .phone {
  filter: drop-shadow(0 0 25px rgb(255 69 105 / 12%));
}

.feature[data-feature="referrals"] .phone {
  filter: drop-shadow(0 0 25px rgb(255 45 85 / 15%));
}

.phone::before {
  position: absolute;
  inset: -15px;
  z-index: -1;
  content: '';
  background: radial-gradient(
    ellipse,
    rgb(255 69 105 / 8%) 0%,
    rgb(255 23 68 / 4%) 40%,
    transparent 70%
  );
  filter: blur(20px);
  border-radius: calc(var(--phone-border-radius) + 15px);
  opacity: 0.5;
  animation: feature-ambient-glow 15s ease-in-out infinite;
}

.feature[data-feature="referrals"] .phone::before {
  background: radial-gradient(ellipse,
  rgb(255 45 85 / 10%) 0%,
  rgb(255 45 85 / 5%) 40%,
  transparent 70%
);
}

.phone__screen {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #000;
  border: 1px solid rgb(255 255 255 / 6%);
  border-radius: var(--phone-border-radius);
}

.phone__content {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #000;
  border-radius: var(--phone-border-radius);
}

/* ===== OPTIMIZACIONES MVILES PARA CARACTERSTICAS ===== */
@media screen and (width <= 1023px) {
  /* ===== MOSTRAR IMGENES DE CARACTERSTICAS EN MVIL ===== */
  .feature__phone {
    display: block !important;
    order: 1 !important;
    width: var(--mobile-feature-image-width) !important;
    height: var(--mobile-feature-image-height) !important;
    margin: 0 auto var(--space-xl) auto !important;
    visibility: visible !important;
    filter: drop-shadow(0 8px 20px rgb(255 69 105 / 20%)) !important;
    opacity: 1 !important;
    transform: none !important;
  }

  .phone {
    display: block !important;
    visibility: visible !important;
    background: linear-gradient(135deg,
    rgb(255 69 105 / 3%) 0%,
    rgb(0 0 0 / 97%) 15%,
    rgb(0 0 0 / 100%) 85%,
    rgb(255 69 105 / 3%) 100%
  ) !important;
    border-radius: var(--mobile-feature-image-border-radius) !important;
    box-shadow:
      0 15px 40px rgb(0 0 0 / 30%),
      0 5px 15px rgb(0 0 0 / 20%),
      0 0 0 1px rgb(255 255 255 / 8%) !important;
    opacity: 1 !important;
  }

  .phone__screen,
  .phone__content {
    display: block !important;
    visibility: visible !important;
    border-radius: var(--mobile-feature-image-border-radius) !important;
    opacity: 1 !important;
  }

  .phone__app-image {
    display: block !important;
    visibility: visible !important;
    filter: brightness(1.05) contrast(1.08) saturate(1.1) !important;
    border-radius: var(--mobile-feature-image-border-radius) !important;
    opacity: 1 !important;
  }

  /* ===== EFECTOS AMBIENTALES MVILES ===== */
  .feature__phone::before {
    display: block !important;
    visibility: visible !important;
    border-radius: calc(var(--mobile-feature-image-border-radius) + 3px) !important;
    opacity: 0.6 !important;
  }

  .phone::before {
    inset: -10px !important;
    display: block !important;
    visibility: visible !important;
    filter: blur(15px) !important;
    border-radius: calc(var(--mobile-feature-image-border-radius) + 10px) !important;
    opacity: 0.3 !important;
  }

  /* ===== LAYOUT MVIL PARA CARACTERSTICAS ===== */
  .feature {
    display: flex !important;
    flex-direction: column !important;
    grid-template-columns: none !important;
    gap: var(--space-2xl) !important;
    text-align: center !important;
  }

  .feature:nth-child(even) {
    direction: ltr !important;
    flex-direction: column !important;
  }

  .feature:nth-child(even) > * {
    direction: ltr !important;
  }

  .feature__content {
    order: 2 !important;
    text-align: center !important;
  }

  .feature__title {
    margin-bottom: var(--space-lg) !important;
    font-size: clamp(2rem, 6vw, 2.5rem) !important;
    text-align: center !important;
  }

  .feature__description {
    max-width: 100% !important;
    margin-bottom: var(--space-lg) !important;
    font-size: clamp(1rem, 4vw, 1.2rem) !important;
    text-align: left !important;
  }

  .feature__list {
    max-width: 100% !important;
    text-align: left !important;
  }

  .feature__list-item {
    padding: var(--space-sm) 0 !important;
    margin-bottom: var(--space-md) !important;
    font-size: clamp(0.9rem, 3.5vw, 1rem) !important;
    text-align: left !important;
  }

  /* ===== AJUSTAR GRID DE CARACTERSTICAS ===== */
  .features__grid {
    gap: var(--space-5xl) !important;
  }
}

/* ===== HERO MVIL - OCULTAR TELFONO DESKTOP ===== */
@media screen and (width <= 1023px) {
  .hero__phone,
  .hero__phone-container,
  .hero__phone-screen,
  .hero__phone-content,
  .hero__phone-video,
  .hero__phone-app-image {
    display: none !important;
    visibility: hidden !important;
    opacity: 0 !important;
  }

  .hero__media {
    display: none !important;
    visibility: hidden !important;
    opacity: 0 !important;
  }

  .hero__mobile-video {
    display: block !important;
    width: 100% !important;
    max-width: 320px !important;
    margin: var(--space-xl) auto 0 !important;
    overflow: hidden !important;
    visibility: visible !important;
    filter: drop-shadow(0 0 15px rgb(255 69 105 / 10%)) !important;
    border-radius: 20px !important;
    box-shadow:
      0 15px 40px rgb(0 0 0 / 30%),
      0 5px 15px rgb(0 0 0 / 20%),
      0 0 0 1px rgb(255 255 255 / 5%) !important;
    opacity: 1 !important;
  }

  .hero__mobile-video-player {
    display: block !important;
    width: 100% !important;
    height: auto !important;
    object-fit: cover !important;
    filter: brightness(1.02) contrast(1.05) saturate(1.08) !important;
    border-radius: 20px !important;
  }

  .hero__container {
    grid-template-columns: 1fr !important;
    gap: var(--space-3xl) !important;
    text-align: center !important;
  }

  .hero__content {
    order: 2 !important;
  }
}

/* ===== SECCIN DE VIDEOS FUTURISTA ===== */
.videos {
  position: relative;
