  var(--starflex-crimson-bright) 100%
);
  border-radius: 2px;
  box-shadow: 0 0 10px var(--starflex-crimson-glow);
  transition: width 0.1s ease;
}

.videos__time-display {
  display: flex;
  gap: var(--space-sm);
  font-family: var(--font-mono);
  font-size: 0.9rem;
  font-weight: 600;
  color: white;
}

.videos__current-time {
  color: var(--starflex-crimson-bright);
}

.videos__duration {
  color: rgb(255 255 255 / 70%);
}

.videos__info {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: var(--space-2xl);
  align-items: center;
}

.videos__info-main h3 {
  margin-bottom: var(--space-md);
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--starflex-red) 100%);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.videos__info-description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.videos__cta {
  position: relative;
  width: 100%;
  padding: var(--space-4xl);
  overflow: hidden;
  text-align: center;
  pointer-events: auto;
  background: var(--glass-bg);
  backdrop-filter: blur(25px);
  border: 1px solid var(--border-subtle);
  border-radius: 30px;
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 1s ease-out 1s forwards;
}

.videos__cta::before {
  position: absolute;
  top: 0;
  left: -100%;
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(184 0 46 / 5%),
  transparent
);
  animation: cta-shimmer 8s infinite;
}

.videos__cta-content {
  position: relative;
  z-index: 2;
  pointer-events: auto;
}

.videos__cta-title {
  margin-bottom: var(--space-lg);
  font-size: 2.5rem;
  font-weight: 700;
  background: linear-gradient(135deg,
  var(--text-primary) 0%,
  var(--starflex-crimson-bright) 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.videos__cta-description {
  max-width: 600px;
  margin-right: auto;
  margin-bottom: var(--space-2xl);
  margin-left: auto;
  font-size: 1.2rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.videos__cta-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-xl);
  align-items: center;
  justify-content: center;
}

.videos__cta-buttons .btn {
  position: relative;
  z-index: 10;
  pointer-events: auto;
  cursor: pointer;
}

.videos__cta-buttons a {
  position: relative;
  z-index: 10;
  pointer-events: auto;
}

.btn--cta-video {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  align-items: center;
  min-width: 200px;
  padding: var(--space-lg) var(--space-2xl);
  font-size: 1.1rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 0 15px rgb(255 255 255 / 60%);
  background: linear-gradient(135deg,
  var(--starflex-crimson) 0%,
  var(--starflex-crimson-bright) 100%
);
  border: 2px solid rgb(255 69 105 / 40%);
  border-radius: 16px;
  box-shadow:
    var(--shadow-crimson),
    var(--glow-medium);
}

.btn--cta-video:hover {
  box-shadow:
    var(--shadow-crimson-hover),
    var(--glow-strong);
  transform: translateY(-4px) scale(1.05);
}

.btn--cta-video span {
  font-weight: 800;
  letter-spacing: 1px;
}

.btn--cta-video small {
  font-family: var(--font-mono);
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: 0.9;
}

/* ===== FAQ SECTION COMPACTA ===== */
.faq {
  position: relative;
  width: 100%;
  padding: var(--section-padding-y) var(--section-padding-x);
  overflow: hidden;
  background: linear-gradient(180deg, var(--secondary-bg) 0%, var(--primary-bg) 100%);
}

.faq::before {
  position: absolute;
  inset: 0;
  content: '';
  background: radial-gradient(circle at 50% 50%, rgb(255 69 105 / 2%) 0%, transparent 50%);
  animation: breathe 12s ease-in-out infinite;
}

.faq__header {
  position: relative;
  z-index: 2;
  margin-bottom: var(--space-5xl);
  text-align: center;
}

.faq__title {
  position: relative;
  margin-bottom: var(--space-xl);
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 700;
  letter-spacing: -0.02em;
  background: linear-gradient(135deg, var(--text-primary) 0%, var(--starflex-red) 100%);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.faq__subtitle {
  max-width: 700px;
  margin: 0 auto var(--space-3xl);
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-secondary);
}

.faq__search-container {
  position: relative;
  max-width: 600px;
  margin: 0 auto var(--space-3xl);
}

.faq__search {
  width: 100%;
  padding: var(--space-md) var(--space-lg) var(--space-md) 50px;
  font-family: var(--font-primary);
  font-size: 1rem;
  color: var(--text-primary);
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-subtle);
  border-radius: 20px;
  outline: none;
  transition: all var(--transition);
}

.faq__search::placeholder {
  font-style: italic;
  color: var(--text-muted);
}

.faq__search:focus {
  border-color: var(--starflex-red);
  box-shadow: 0 0 0 3px rgb(255 69 105 / 10%);
}

.faq__search-icon {
  position: absolute;
  top: 50%;
  left: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  font-size: 12px;
  background: var(--starflex-red);
  border-radius: 50%;
  transition: all var(--transition);
  transform: translateY(-50%);
}

.faq__search-icon::before {
  color: white;
  content: '';
}

.faq__search:focus + .faq__search-icon {
  background: var(--starflex-crimson-glow);
}

.faq__container {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  overflow: hidden;
}

.faq__list {
  display: grid;
  gap: var(--space-lg);
}

.faq__item {
  position: relative;
  overflow: hidden;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-subtle);
  border-radius: 18px;
  opacity: 0;
  transition: all var(--transition);
  transform: translateY(20px);
  animation: fadeInUp 0.6s ease-out forwards;
}

.faq__item:nth-child(1) { animation-delay: 0.1s; }
.faq__item:nth-child(2) { animation-delay: 0.15s; }
.faq__item:nth-child(3) { animation-delay: 0.2s; }
.faq__item:nth-child(4) { animation-delay: 0.25s; }
.faq__item:nth-child(5) { animation-delay: 0.3s; }

.faq__item:hover {
  border-color: var(--border-accent);
  box-shadow: var(--shadow-lg);
  transform: translateY(-2px);
}

.faq__item.active {
  border-color: var(--starflex-red);
  box-shadow: var(--shadow-xl);
}

.faq__question {
  position: relative;
  display: flex;
  gap: var(--space-lg);
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: var(--space-lg);
  font-family: var(--font-primary);
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  text-align: left;
  cursor: pointer;
  background: transparent;
  border: none;
  transition: all var(--transition);
}

.faq__question:hover {
  color: var(--starflex-red);
}

.faq__question-text {
  flex: 1;
  line-height: 1.4;
}

.faq__icon {
  position: relative;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, var(--starflex-red), var(--starflex-crimson-glow));
  border-radius: 50%;
  transition: all var(--transition);
}

.faq__icon::before,
.faq__icon::after {
  position: absolute;
  content: '';
  background: white;
  border-radius: 1px;
  transition: all var(--transition);
}

.faq__icon::before {
  width: 12px;
  height: 2px;
}

.faq__icon::after {
  width: 2px;
  height: 12px;
}

.faq__item.active .faq__icon {
  transform: rotate(45deg);
}

.faq__item.active .faq__icon::after {
  opacity: 0;
}

.faq__answer {
  max-height: 0;
  overflow: hidden;
  background: rgb(0 0 0 / 10%);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.faq__answer.active {
  max-height: 500px;
  padding: 0 var(--space-lg) var(--space-lg);
}

.faq__answer-content {
  position: relative;
  padding-top: var(--space-md);
  border-top: 1px solid var(--border-subtle);
}

.faq__answer-text {
  margin: 0;
  font-size: 1rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

.faq__answer-highlight {
  padding: 2px 8px;
  font-weight: 600;
  color: var(--starflex-red);
  background: rgb(255 69 105 / 8%);
  border-radius: 6px;
}

.faq__no-results {
  display: none;
  padding: var(--space-3xl);
  color: var(--text-muted);
  text-align: center;
}

.faq__no-results.show {
  display: block;
}

.faq__no-results-text {
  margin-bottom: var(--space-md);
  font-size: 1.1rem;
}

.faq__no-results-suggestion {
  font-size: 1rem;
  color: var(--text-secondary);
}

/* ===== SECCIN DE CONTACTO OPTIMIZADA ===== */
.contact {
  position: relative;
  width: 100%;
  padding: var(--section-padding-y) var(--section-padding-x);
  overflow: hidden;
  background: linear-gradient(180deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
}

.contact::before {
  position: absolute;
  inset: 0;
  z-index: 1;
  content: '';
  background:
    radial-gradient(ellipse at 30% 20%, rgb(255 69 105 / 4%) 0%, transparent 60%),
    radial-gradient(ellipse at 70% 80%, rgb(184 0 46 / 3%) 0%, transparent 60%);
}

.contact__bg-effects {
  position: absolute;
  inset: 0;
  z-index: 1;
  pointer-events: none;
}

.contact__orb {
  position: absolute;
  filter: blur(80px);
  border-radius: 50%;
  opacity: 0.15;
}

.contact__orb--1 {
  top: 20%;
  left: 10%;
  width: 300px;
  height: 300px;
  background: radial-gradient(circle, rgb(255 69 105 / 60%) 0%, transparent 70%);
}

.contact__orb--2 {
  right: 10%;
  bottom: 20%;
  width: 250px;
  height: 250px;
  background: radial-gradient(circle, rgb(184 0 46 / 50%) 0%, transparent 70%);
}

.contact__orb--3 {
  top: 60%;
  left: 50%;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgb(139 92 246 / 40%) 0%, transparent 70%);
  transform: translateX(-50%);
}

.contact__header {
  position: relative;
  z-index: 3;
  margin-bottom: var(--space-5xl);
  text-align: center;
  opacity: 0;
  transform: translateY(30px);
  animation: contact-fadeIn 1s ease-out 0.2s forwards;
}

.contact__badge {
  display: inline-flex;
  gap: var(--space-sm);
  align-items: center;
  padding: var(--space-sm) var(--space-xl);
  margin-bottom: var(--space-2xl);
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--starflex-red);
  text-transform: uppercase;
  letter-spacing: 1px;
  pointer-events: none;
  user-select: none;
  background: rgb(255 69 105 / 10%);
  backdrop-filter: blur(20px);
  border: 1px solid rgb(255 69 105 / 20%);
  border-radius: 30px;
  box-shadow: 0 8px 25px rgb(255 69 105 / 15%);
}

.contact__badge-icon {
  font-size: 1.2rem;
  filter: drop-shadow(0 0 8px var(--starflex-red));
}

.contact__title {
  margin-bottom: var(--space-xl);
  font-size: clamp(2.8rem, 7vw, 5rem);
  font-weight: 800;
  line-height: 0.9;
  letter-spacing: -0.02em;
}

.contact__title--main {
  display: block;
  margin-bottom: var(--space-sm);
  font-weight: 900;
  background: linear-gradient(135deg,
  #fff 0%,
  #e2e8f0 50%,
  #cbd5e1 100%
);
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact__title--highlight {
  position: relative;
  display: block;
  font-weight: 900;
  background: linear-gradient(135deg,
  var(--starflex-red) 0%,
  var(--starflex-crimson-glow) 100%
);
  filter: drop-shadow(0 0 20px rgb(255 69 105 / 30%));
  background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact__title--highlight::after {
  position: absolute;
  bottom: -8px;
  left: 50%;
  width: 60%;
  height: 3px;
  content: '';
  background: linear-gradient(90deg,
  transparent 0%,
  var(--starflex-red) 50%,
  transparent 100%
);
  border-radius: 2px;
  opacity: 0.8;
  transform: translateX(-50%);
}

.contact__subtitle {
  max-width: 800px;
  margin: 0 auto;
  font-size
: 1.25rem;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-secondary);
}

.contact__grid {
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--space-2xl);
  width: 100%;
  max-width: 1200px;
  margin: 0 auto var(--space-5xl);
  overflow: hidden;
}

.contact__channel {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--space-2xl);
  text-align: center;
  cursor: pointer;
  background: rgb(255 255 255 / 2%);
  backdrop-filter: blur(10px);
  border: 1px solid rgb(255 255 255 / 5%);
  border-radius: 20px;
  opacity: 0;
  transition: all var(--transition);
  transform: translateY(40px);
  animation: contact-slideUp 0.8s ease-out forwards;
}

.contact__channel:nth-child(1) { animation-delay: 0.1s; }
.contact__channel:nth-child(2) { animation-delay: 0.2s; }
.contact__channel:nth-child(3) { animation-delay: 0.3s; }
.contact__channel:nth-child(4) { animation-delay: 0.4s; }
.contact__channel:nth-child(5) { animation-delay: 0.5s; }
.contact__channel:nth-child(6) { animation-delay: 0.6s; }

.contact__channel:hover {
  background: rgb(255 255 255 / 5%);
  border-color: rgb(255 255 255 / 10%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%);
  transform: translateY(-10px);
}

.contact__channel-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  margin-bottom: var(--space-xl);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgb(0 0 0 / 20%);
  transition: all var(--transition);
}

.contact__channel-icon svg {
  width: 36px;
  height: 36px;
  color: white;
  filter: drop-shadow(0 2px 4px rgb(0 0 0 / 30%));
  transition: all var(--transition);
}

.contact__channel:hover .contact__channel-icon {
  box-shadow: 0 15px 40px rgb(0 0 0 / 30%);
  transform: scale(1.1);
}

.contact__channel:hover .contact__channel-icon svg {
  transform: scale(1.05);
}

.contact__channel[data-channel="whatsapp"] .contact__channel-icon {
  background: linear-gradient(135deg, #25d366 0%, #128c7e 100%);
}

.contact__channel[data-channel="whatsapp"]:hover {
  border-color: rgb(37 211 102 / 30%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), 0 0 20px rgb(37 211 102 / 20%);
}

.contact__channel[data-channel="instagram"] .contact__channel-icon {
  background: linear-gradient(135deg, #e1306c 0%, #fd1d1d 50%, #fcb045 100%);
}

.contact__channel[data-channel="instagram"]:hover {
  border-color: rgb(225 48 108 / 30%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), 0 0 20px rgb(225 48 108 / 20%);
}

.contact__channel[data-channel="facebook"] .contact__channel-icon {
  background: linear-gradient(135deg, #1877f2 0%, #42a5f5 100%);
}

.contact__channel[data-channel="facebook"]:hover {
  border-color: rgb(24 119 242 / 30%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), 0 0 20px rgb(24 119 242 / 20%);
}

.contact__channel[data-channel="tiktok"] .contact__channel-icon {
  background: linear-gradient(135deg, #ff0050 0%, #000 100%);
}

.contact__channel[data-channel="tiktok"]:hover {
  border-color: rgb(255 0 80 / 30%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), 0 0 20px rgb(255 0 80 / 20%);
}

.contact__channel[data-channel="telegram"] .contact__channel-icon {
  background: linear-gradient(135deg, #08c 0%, #229ed9 100%);
}

.contact__channel[data-channel="telegram"]:hover {
  border-color: rgb(0 136 204 / 30%);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), 0 0 20px rgb(0 136 204 / 20%);
}

.contact__channel[data-channel="email"] .contact__channel-icon {
  background: linear-gradient(135deg, var(--starflex-crimson) 0%, var(--starflex-crimson-bright) 100%);
}

.contact__channel[data-channel="email"]:hover {
  border-color: var(--border-crimson);
  box-shadow: 0 20px 40px rgb(0 0 0 / 20%), var(--glow-medium);
}

.contact__channel-content {
  flex: 1;
}

.contact__channel-title {
  margin-bottom: var(--space-md);
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  transition: all var(--transition);
}

.contact__channel:hover .contact__channel-title {
  color: var(--starflex-red);
}

.contact__channel-description {
  margin-bottom: var(--space-xl);
  font-size: 1rem;
  line-height: 1.5;
  color: var(--text-muted);
  transition: all var(--transition);
}

.contact__channel:hover .contact__channel-description {
  color: var(--text-secondary);
}

.contact__channel-btn {
  display: inline-flex;
  gap: var(--space-sm);
  align-items: center;
  padding: var(--space-md) var(--space-xl);
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-primary);
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  background: rgb(255 255 255 / 8%);
  border: 1px solid rgb(255 255 255 / 10%);
  border-radius: 25px;
  box-shadow: 0 4px 15px rgb(0 0 0 / 10%);
  transition: all var(--transition);
}

.contact__channel-btn:hover {
  color: white;
  background: var(--starflex-red);
  border-color: var(--starflex-red);
  box-shadow: 0 8px 25px rgb(255 69 105 / 30%);
  transform: translateY(-2px);
}

.contact__channel-arrow {
  font-size: 1rem;
  font-weight: bold;
  transition: all var(--transition);
}

.contact__channel-btn:hover .contact__channel-arrow {
  transform: translateX(3px);
}

.contact__footer {
  position: relative;
  z-index: 3;
  padding: var(--space-2xl) 0;
  overflow: visible;
  background: rgb(0 0 0 / 20%);
  backdrop-filter: blur(20px);
  border-top: 1px solid rgb(255 255 255 / 8%);
  opacity: 0;
  transform: translateY(30px);
  animation: contact-fadeIn 1s ease-out 1s forwards;
}

.contact__footer-content {
  display: flex;
  gap: var(--space-2xl);
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  padding: var(--space-md);
  margin: 0 auto;
  overflow: visible;
}

.contact__footer-text h3 {
  margin-bottom: var(--space-xs);
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.contact__footer-text p {
  font-family: var(--font-mono);
  font-size: 1rem;
  color: var(--text-muted);
}

.btn--footer {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
  align-items: center;
  min-width: 180px;
  padding: var(--space-md) var(--space-xl);
  font-size: 1rem;
  font-weight: 700;
  color: #fff;
  background: linear-gradient(135deg,
  var(--starflex-crimson) 0%,
  var(--starflex-crimson-bright) 100%
);
  border: 1px solid rgb(255 69 105 / 30%);
  border-radius: 25px;
  box-shadow:
    0 10px 30px rgb(184 0 46 / 30%),
    inset 0 1px 0 rgb(255 255 255 / 10%);
  transition: all var(--transition);
}

.btn--footer:hover {
  box-shadow:
    0 15px 40px rgb(184 0 46 / 40%),
    var(--glow-medium),
    inset 0 1px 0 rgb(255 255 255 / 20%);
  transform: translateY(-4px);
}

.btn--footer span {
  font-weight: 800;
  letter-spacing: 1px;
}

.btn--footer small {
  font-family: var(--font-mono);
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: 0.9;
}

/* ===== BOTN FLOTANTE FUTURISTA CON CONO DE MENSAJE ===== */
.floating-widget {
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 1100;
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  pointer-events: none;
}

.floating-widget__container {
  position: relative;
  pointer-events: auto;
}

.floating-widget__main-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 80px;
  overflow: hidden;
  cursor: pointer;
  background: linear-gradient(135deg,
  var(--starflex-crimson-dark) 0%,
  var(--starflex-crimson) 25%,
  var(--starflex-crimson-light) 50%,
  var(--starflex-crimson-glow) 75%,
  var(--starflex-crimson-bright) 100%
);
  border: none;
  border-radius: 50%;
  box-shadow:
    var(--shadow-crimson),
    var(--glow-medium),
    inset 0 2px 0 rgb(255 255 255 / 15%);
  transition: all var(--transition);
}

.floating-widget__main-btn::before {
  position: absolute;
  inset: -4px;
  z-index: -1;
  content: '';
  background: linear-gradient(45deg,
  var(--starflex-crimson-glow),
  var(--starflex-crimson-bright),
  var(--starflex-crimson-glow),
  var(--starflex-crimson-bright)
);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.8s ease;
}

.floating-widget__main-btn:hover {
  box-shadow:
    var(--shadow-crimson-hover),
    var(--glow-strong),
    inset 0 2px 0 rgb(255 255 255 / 25%);
  transform: scale(1.15);
}

.floating-widget__main-btn:hover::before {
  opacity: 1;
}

.floating-widget__main-btn.active {
  transform: scale(1.1);
}

.floating-widget__main-icon {
  z-index: 1;
  width: 40px;
  height: 40px;
  color: white;
  filter: drop-shadow(0 2px 8px rgb(0 0 0 / 40%));
  transition: all var(--transition);
}

.floating-widget__main-btn:hover .floating-widget__main-icon {
  filter: drop-shadow(0 4px 12px rgb(0 0 0 / 60%)) brightness(1.3) saturate(1.2);
  transform: scale(1.2);
}

.floating-widget__main-btn.active .floating-widget__main-icon {
  filter: drop-shadow(0 2px 8px rgb(0 0 0 / 40%)) brightness(1.1);
  transform: scale(0.9);
}

.floating-widget__menu {
  position: absolute;
  right: 0;
  bottom: 90px;
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: translateY(20px) scale(0.8);
}

.floating-widget__menu.active {
  pointer-events: auto;
  visibility: visible;
  opacity: 1;
  transform: translateY(0) scale(1);
}

.floating-widget__menu-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  overflow: hidden;
  text-decoration: none;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  box-shadow:
    0 8px 25px rgb(0 0 0 / 30%),
    inset 0 1px 0 rgb(255 255 255 / 10%);
  transition: all var(--transition);
}

.floating-widget__menu-item::before {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  content: '';
  background: linear-gradient(90deg,
  transparent,
  rgb(255 255 255 / 30%),
  transparent
);
  border-radius: 50%;
  transition: left 1.2s ease;
}

.floating-widget__menu-item:hover::before {
  left: 100%;
}

.floating-widget__menu-item:hover {
  box-shadow:
    0 12px 35px rgb(0 0 0 / 40%),
    inset 0 1px 0 rgb(255 255 255 / 20%);
  transform: scale(1.15);
}

.floating-widget__whatsapp {
  background: linear-gradient(135deg, #25d366 0%, #128c7e 100%);
}

.floating-widget__whatsapp:hover {
  box-shadow:
    0 12px 35px rgb(37 211 102 / 40%),
    0 0 25px rgb(37 211 102 / 30%),
    inset 0 1px 0 rgb(255 255 255 / 20%);
}

.floating-widget__telegram {
  background: linear-gradient(135deg, #08c 0%, #229ed9 100%);
}

.floating-widget__telegram:hover {
  box-shadow:
    0 12px 35px rgb(0 136 204 / 40%),
    0 0 25px rgb(0 136 204 / 30%),
    inset 0 1px 0 rgb(255 255 255 / 20%);
}

.floating-widget__icon {
  width: 32px;
  height: 32px;
  color: white;
  filter: drop-shadow(0 2px 4px rgb(0 0 0 / 30%));
  transition: all var(--transition);
}

.floating-widget__menu-item:hover .floating-widget__icon {
  transform: scale(1.1);
}

.floating-widget__tooltip {
  position: absolute;
  top: 50%;
  right: 75px;
  z-index: 10;
  padding: var(--space-sm) var(--space-lg);
  font-size: 0.85rem;
  font-weight: 600;
  color: white;
  white-space: nowrap;
  pointer-events: none;
  visibility: hidden;
  background: linear-gradient(135deg,
  rgb(0 0 0 / 95%) 0%,
  rgb(21 25 34 / 95%) 100%
);
  backdrop-filter: blur(15px);
  border: 1px solid rgb(255 255 255 / 10%);
  border-radius: 12px;
  box-shadow:
    0 8px 25px rgb(0 0 0 / 40%),
    0 0 15px rgb(255 69 105 / 20%);
  opacity: 0;
  transition: all var(--transition);
  transform: translateY(-50%);
}

.floating-widget__tooltip::after {
  position: absolute;
  top: 50%;
  left: 100%;
  content: '';
  border: 8px solid transparent;
  border-left-color: rgb(0 0 0 / 95%);
  transform: translateY(-50%);
}

.floating-widget__menu-item:hover .floating-widget__tooltip {
  visibility: visible;
  opacity: 1;
  transform: translateY(-50%) translateX(-8px);
}

/* ===== RESPONSIVE PARA MVILES ===== */
@media (width <= 1024px) {
  .nav__toggle {
    display: flex;
  }

  .nav__cta {
    display: none;
  }

  /* ===== NUEVO DRAWER MODERNO TIPO SIDEBAR (70% ANCHO) ===== */
  .nav__menu {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: calc(var(--navbar-z-index) + 3);
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    width: min(var(--drawer-width), var(--drawer-max-width));
    height: 100vh;
    height: 100dvh;
    padding: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px) 0 max(calc(env(safe-area-inset-bottom, 20px) + 20px), 30px) 0;
    overflow-y: auto;
    pointer-events: auto;
    background: linear-gradient(135deg,
    rgb(10 13 20 / 98%) 0%,
    rgb(21 25 34 / 98%) 50%,
    rgb(30 35 48 / 98%) 100%
  );
    backdrop-filter: blur(30px);
    backdrop-filter: blur(30px);
    border-right: 1px solid rgb(255 69 105 / 20%);
    box-shadow:
      0 0 50px rgb(0 0 0 / 50%),
      0 0 100px rgb(255 69 105 / 10%);
    transition: left var(--drawer-transition);
    transform: none;
    -webkit-overflow-scrolling: touch;
  }

  .nav__menu.active {
    left: 0;
  }

  /* ===== OVERLAY OSCURECIDO PARA EL 30% RESTANTE ===== */
  .nav__menu::before {
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    height: 100dvh;
    pointer-events: none;
    visibility: hidden;
    content: '';
    background: var(--drawer-overlay-bg);
    backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    opacity: 0;
    transition: all var(--drawer-transition);
  }

  .nav__menu.active::before {
    pointer-events: auto;
    visibility: visible;
    opacity: 1;
  }

  /* ===== BOTN DE CERRAR MODERNO ===== */
  .nav__close {
    position: absolute;
    top: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px);
    right: 20px;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 10%) 0%,
    rgb(21 25 34 / 90%) 100%
  );
    backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    border: 1px solid rgb(255 45 85 / 30%);
    border-radius: 12px;
    box-shadow:
      0 4px 15px rgb(0 0 0 / 30%),
      0 0 10px rgb(255 45 85 / 20%);
    transition: all var(--drawer-transition);
  }

  .nav__close:hover {
    background: linear-gradient(135deg,
    rgb(255 45 85 / 20%) 0%,
    rgb(21 25 34 / 95%) 100%
  );
    border-color: rgb(255 45 85 / 50%);
    box-shadow:
      0 6px 20px rgb(0 0 0 / 40%),
      0 0 15px rgb(255 45 85 / 30%);
    transform: scale(1.05);
  }

  .nav__close svg {
    width: 20px;
    height: 20px;
    color: var(--text-primary);
    transition: all var(--drawer-transition);
  }

  .nav__close:hover svg {
    color: var(--starflex-crimson-bright);
    transform: rotate(90deg);
  }

  /* ===== SELECTOR DE IDIOMA COMPACTO ===== */
  .nav__language-mobile {
    width: calc(100% - 40px);
    padding: 20px;
    margin: 20px 20px 30px;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 8%) 0%,
    rgb(21 25 34 / 90%) 100%
  );
    backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    border: 1px solid rgb(255 45 85 / 20%);
    border-radius: 16px;
    box-shadow:
      0 8px 25px rgb(0 0 0 / 30%),
      0 0 15px rgb(255 45 85 / 10%);
  }

  .nav__language-title {
    margin-bottom: 15px;
    font-family: var(--font-mono);
    font-size: 0.85rem;
    font-weight: 600;
    color: var(--starflex-crimson-bright);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .nav__language-options {
    display: flex;
    gap: 10px;
  }

  .nav__language-option {
    display: flex;
    flex: 1;
    gap: 8px;
    align-items: center;
    justify-content: center;
    min-height: 44px;
    padding: 12px;
    font-size: 0.8rem;
    font-weight: 500;
    color: var(--text-secondary);
    cursor: pointer;
    background: rgb(255 255 255 / 5%);
    border: 1px solid rgb(255 45 85 / 20%);
    border-radius: 12px;
    transition: all var(--drawer-transition);
  }

  .nav__language-option:hover {
    color: var(--text-primary);
    background: rgb(255 45 85 / 10%);
    border-color: rgb(255 45 85 / 40%);
    transform: translateY(-1px);
  }

  .nav__language-option.active {
    color: var(--starflex-crimson-bright);
    background: rgb(255 45 85 / 15%);
    border-color: rgb(255 45 85 / 50%);
  }

  .nav__language-flag {
    font-size: 1.1rem;
  }

  .nav__language-name {
    font-weight: 600;
  }

  /* ===== LISTA DE NAVEGACIN COMPACTA ===== */
  .nav__list {
    flex-direction: column;
    gap: 8px;
    width: 100%;
    padding: 0 20px;
    margin-bottom: 30px;
    background: transparent;
    border: none;
    box-shadow: none;
  }

  .nav__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    min-height: 48px;
    padding: 16px 20px;
    overflow: hidden;
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--text-secondary);
    text-align: left;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    background: rgb(255 255 255 / 3%);
    backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border: 1px solid rgb(255 255 255 / 8%);
    border-radius: 12px;
    transition: all var(--drawer-transition);
  }

  .nav__link::before {
    background: linear-gradient(135deg,
    rgb(255 45 85 / 10%) 0%,
    rgb(255 45 85 / 5%) 100%
  );
    border-radius: 12px;
  }

  .nav__link:hover {
    color: var(--text-primary);
    background: rgb(255 45 85 / 8%);
    border-color: rgb(255 45 85 / 30%);
    box-shadow:
      0 4px 15px rgb(255 45 85 / 20%),
      inset 0 1px 0 rgb(255 255 255 / 10%);
    transform: translateX(5px);
  }

  .nav__link.active {
    color: var(--starflex-crimson-bright);
    background: rgb(255 45 85 / 12%);
    border-color: rgb(255 45 85 / 40%);
    box-shadow:
      0 0 20px rgb(255 45 85 / 30%),
      inset 0 1px 0 rgb(255 255 255 / 15%);
    transform: translateX(8px);
  }

  /* ===== CTA MVIL COMPACTO ===== */
  .nav__cta-mobile {
    padding: 0 20px;
  }

  .btn--mobile {
    width: 100%;
    min-height: 52px;
    padding: 16px 24px;
    font-size: 0.95rem;
    font-weight: 700;
    color: #fff;
    text-shadow: 0 0 10px rgb(255 255 255 / 60%);
    text-transform: uppercase;
    letter-spacing: 1px;
    background: linear-gradient(135deg,
    var(--starflex-crimson) 0%,
    var(--starflex-crimson-bright) 100%
  );
    border: 2px solid rgb(255 45 85 / 40%);
    border-radius: 14px;
    box-shadow:
      0 8px 25px rgb(255 45 85 / 40%),
      0 0 20px rgb(255 45 85 / 30%);
    transition: all var(--drawer-transition);
  }

  .btn--mobile:hover {
    box-shadow:
      0 12px 35px rgb(255 45 85 / 50%),
      0 0 30px rgb(255 45 85 / 40%);
    transform: translateY(-2px);
  }

  /* ===== RESPONSIVE PARA CONTACTO ===== */
  .contact__grid {
    grid-template-columns: 1fr;
    gap: var(--space-xl);
  }

  .contact__footer-content {
    flex-direction: column;
    gap: var(--space-xl);
    text-align: center;
  }
}

/* ===== ELEMENTOS ESPECFICOS PARA MVIL ===== */
.nav__close,
.nav__language-mobile,
.nav__cta-mobile {
  display: none;
}

/* EN DESKTOP - FORZAR OCULTACIN DE ELEMENTOS MVILES */
@media (width >= 1024px) {
  .nav__close,
  .nav__language-mobile,
  .nav__cta-mobile {
    display: none !important;
  }

  .nav__toggle {
    display: none !important;
  }
}

/* EN MVIL - MOSTRAR ELEMENTOS MVILES Y OCULTAR DESKTOP */
@media (width <= 1023px) {
  .nav__close,
  .nav__language-mobile,
  .nav__cta-mobile {
    display: block;
  }

  .nav__toggle {
    display: flex;
  }

  .nav__cta {
    display: none;
  }

  .language-switcher {
    display: none;
  }
}

