    background: linear-gradient(135deg,
    var(--mobile-glass-bg) 0%,
    rgb(30 35 48 / 90%) 100%
  ) !important;
    backdrop-filter: blur(15px) !important;
    backdrop-filter: blur(15px) !important;
    border: 1px solid var(--mobile-neon-border) !important;
    box-shadow:
      0 4px 15px rgb(0 0 0 / 30%),
      0 0 10px var(--mobile-neon-glow-soft) !important;
    transition: all 0.3s ease !important;
  }

  .nav__toggle:hover,
  .nav__toggle:active {
    background: linear-gradient(135deg,
    rgb(21 25 34 / 95%) 0%,
    rgb(30 35 48 / 95%) 100%
  ) !important;
    border-color: var(--mobile-neon-red) !important;
    box-shadow:
      0 6px 20px rgb(0 0 0 / 40%),
      0 0 15px var(--mobile-neon-glow) !important;
    transform: translateY(-1px) !important;
  }

  .nav__toggle.active {
    background: linear-gradient(135deg,
    rgb(21 25 34 / 98%) 0%,
    rgb(30 35 48 / 98%) 100%
  ) !important;
    border-color: var(--mobile-neon-red-bright) !important;
    box-shadow:
      0 8px 25px rgb(0 0 0 / 50%),
      0 0 20px var(--mobile-neon-glow) !important;
  }

  /* Lneas del hamburguesa con efecto nen sutil */
  .nav__toggle-line {
    background: #fff !important;
    box-shadow: 0 0 5px rgb(255 255 255 / 30%) !important;
    transition: all 0.3s ease !important;
  }

  .nav__toggle.active .nav__toggle-line:nth-child(1),
  .nav__toggle.active .nav__toggle-line:nth-child(3) {
    background: var(--mobile-neon-red-bright) !important;
    box-shadow: 0 0 8px var(--mobile-neon-glow) !important;
  }

  /* ===== MEN MVIL - FONDO FUTURISTA CON ACENTOS ROJIZOS ===== */
  .nav__menu {
    background: linear-gradient(135deg,
    rgb(10 13 20 / 98%) 0%,
    rgb(21 25 34 / 98%) 50%,
    rgb(30 35 48 / 98%) 100%
  ) !important;
    backdrop-filter: blur(25px) !important;
    backdrop-filter: blur(25px) !important;
    border-right: 2px solid var(--mobile-neon-border) !important;
    box-shadow:
      0 0 50px rgb(0 0 0 / 80%),
      inset 2px 0 0 var(--mobile-neon-glow-soft) !important;
  }

  /* Efecto de resplandor sutil en el borde del men */
  .nav__menu::before {
    position: absolute !important;
    top: 0 !important;
    right: 0 !important;
    width: 2px !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(180deg,
    transparent 0%,
    var(--mobile-neon-red) 20%,
    var(--mobile-neon-red-bright) 50%,
    var(--mobile-neon-red) 80%,
    transparent 100%
  ) !important;
    opacity: 0.6 !important;
    animation: neon-pulse-mobile 3s ease-in-out infinite !important;
  }

  /* ===== BOTN DE CERRAR - ESTILO NEN ROJIZO ===== */
  .nav__close {
    background: linear-gradient(135deg,
    var(--mobile-glass-bg) 0%,
    rgb(30 35 48 / 90%) 100%
  ) !important;
    backdrop-filter: blur(15px) !important;
    backdrop-filter: blur(15px) !important;
    border: 1px solid var(--mobile-neon-border) !important;
    box-shadow:
      0 4px 15px rgb(0 0 0 / 30%),
      0 0 10px var(--mobile-neon-glow-soft) !important;
    transition: all 0.3s ease !important;
  }

  .nav__close:hover,
  .nav__close:active {
    background: linear-gradient(135deg,
    rgb(21 25 34 / 95%) 0%,
    rgb(30 35 48 / 95%) 100%
  ) !important;
    border-color: var(--mobile-neon-red) !important;
    box-shadow:
      0 6px 20px rgb(0 0 0 / 40%),
      0 0 15px var(--mobile-neon-glow) !important;
    transform: scale(1.05) !important;
  }

  .nav__close svg {
    color: #fff !important;
    filter: drop-shadow(0 0 3px rgb(255 255 255 / 30%)) !important;
    transition: all 0.3s ease !important;
  }

  .nav__close:hover svg,
  .nav__close:active svg {
    color: var(--mobile-neon-red-bright) !important;
    filter: drop-shadow(0 0 6px var(--mobile-neon-glow)) !important;
  }

  /* ===== SELECTOR DE IDIOMA MVIL - ESTTICA FUTURISTA ===== */
  .nav__language-mobile {
    background: linear-gradient(135deg,
    rgb(255 45 85 / 10%) 0%,
    var(--mobile-glass-bg) 20%,
    rgb(30 35 48 / 95%) 100%
  ) !important;
    backdrop-filter: blur(20px) !important;
    backdrop-filter: blur(20px) !important;
    border: 1px solid var(--mobile-neon-border) !important;
    box-shadow:
      0 8px 25px rgb(0 0 0 / 30%),
      0 0 15px var(--mobile-neon-glow-soft),
      inset 0 1px 0 rgb(255 255 255 / 10%) !important;
  }

  .nav__language-title {
    font-weight: 700 !important;
    color: var(--mobile-neon-red-bright) !important;
    text-shadow: 0 0 8px var(--mobile-neon-glow) !important;
  }

  /* Opciones de idioma con efecto nen */
  .nav__language-option {
    color: rgb(255 255 255 / 90%) !important;
    background: transparent !important;
    backdrop-filter: blur(10px) !important;
    backdrop-filter: blur(10px) !important;
    border: 1px solid rgb(255 45 85 / 20%) !important;
    box-shadow: 0 2px 8px rgb(0 0 0 / 20%) !important;
    transition: all 0.3s ease !important;
  }

  .nav__language-option:hover,
  .nav__language-option:active {
    color: #fff !important;
    background: rgb(255 45 85 / 15%) !important;
    border-color: var(--mobile-neon-red) !important;
    box-shadow:
      0 4px 12px rgb(0 0 0 / 30%),
      0 0 10px var(--mobile-neon-glow-soft) !important;
    transform: translateY(-1px) !important;
  }

  .nav__language-option.active {
    color: var(--mobile-neon-red-bright) !important;
    text-shadow: 0 0 8px var(--mobile-neon-glow) !important;
    background: rgb(255 45 85 / 20%) !important;
    border-color: var(--mobile-neon-red-bright) !important;
    box-shadow:
      0 0 15px var(--mobile-neon-glow),
      inset 0 1px 0 rgb(255 255 255 / 20%) !important;
  }

  /* ===== ENLACES DE NAVEGACIN - ESTILO FUTURISTA ROJIZO ===== */
  .nav__list {
    background: transparent !important;
  }

  .nav__link {
    position: relative !important;
    overflow: hidden !important;
    color: rgb(255 255 255 / 90%) !important;
    background: rgb(255 255 255 / 3%) !important;
    backdrop-filter: blur(10px) !important;
    backdrop-filter: blur(10px) !important;
    border: 1px solid rgb(255 255 255 / 10%) !important;
    box-shadow:
      0 2px 8px rgb(0 0 0 / 20%),
      inset 0 1px 0 rgb(255 255 255 / 5%) !important;
    transition: all 0.3s ease !important;
  }

  /* Efecto de lnea nen en hover */
  .nav__link::before {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 3px !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(180deg,
    transparent 0%,
    var(--mobile-neon-red) 50%,
    transparent 100%
  ) !important;
    opacity: 0 !important;
    transition: opacity 0.3s ease !important;
  }

  .nav__link:hover,
  .nav__link:active {
    color: #fff !important;
    background: rgb(255 45 85 / 10%) !important;
    border-color: var(--mobile-neon-border) !important;
    box-shadow:
      0 4px 15px rgb(0 0 0 / 30%),
      0 0 10px var(--mobile-neon-glow-soft),
      inset 0 1px 0 rgb(255 255 255 / 10%) !important;
    transform: translateX(3px) !important;
  }

  .nav__link:hover::before,
  .nav__link:active::before {
    opacity: 1 !important;
  }

  .nav__link.active {
    color: var(--mobile-neon-red-bright) !important;
    text-shadow: 0 0 8px var(--mobile-neon-glow) !important;
    background: rgb(255 45 85 / 15%) !important;
    border-color: var(--mobile-neon-red) !important;
    box-shadow:
      0 0 20px var(--mobile-neon-glow),
      inset 0 1px 0 rgb(255 255 255 / 15%) !important;
    transform: translateX(5px) !important;
  }

  .nav__link.active::before {
    background: linear-gradient(180deg,
    var(--mobile-neon-red-bright) 0%,
    var(--mobile-neon-red) 50%,
    var(--mobile-neon-red-bright) 100%
  ) !important;
    box-shadow: 0 0 8px var(--mobile-neon-glow) !important;
    opacity: 1 !important;
  }

  /* ===== BOTN CTA MVIL - NEN ROJIZO PRINCIPAL ===== */
  .nav__cta-mobile {
    background: transparent !important;
  }

  .btn--mobile {
    position: relative !important;
    overflow: hidden !important;
    color: #fff !important;
    text-shadow: 0 0 10px rgb(255 255 255 / 80%) !important;
    background: linear-gradient(135deg,
    var(--mobile-neon-red) 0%,
    var(--mobile-neon-red-dark) 50%,
    var(--mobile-neon-red) 100%
  ) !important;
    border: 2px solid var(--mobile-neon-border) !important;
    box-shadow:
      0 8px 25px rgb(255 45 85 / 40%),
      0 0 20px var(--mobile-neon-glow),
      inset 0 1px 0 rgb(255 255 255 / 20%) !important;
    transition: all 0.3s ease !important;
  }

  /* Efecto de brillo interno sutil */
  .btn--mobile::before {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent,
    rgb(255 255 255 / 20%),
    transparent
  ) !important;
    transition: left 0.6s ease !important;
  }

  .btn--mobile:hover,
  .btn--mobile:active {
    text-shadow: 0 0 15px rgb(255 255 255 / 100%) !important;
    background: linear-gradient(135deg,
    var(--mobile-neon-red-bright) 0%,
    var(--mobile-neon-red) 50%,
    var(--mobile-neon-red-bright) 100%
  ) !important;
    border-color: var(--mobile-neon-red-bright) !important;
    box-shadow:
      0 12px 35px rgb(255 45 85 / 60%),
      0 0 30px var(--mobile-neon-glow),
      inset 0 1px 0 rgb(255 255 255 / 30%) !important;
    transform: translateY(-2px) !important;
  }

  .btn--mobile:hover::before,
  .btn--mobile:active::before {
    left: 100% !important;
  }

  /* ===== ANIMACIONES NEN OPTIMIZADAS PARA MVIL ===== */
  @keyframes neon-pulse-mobile {
    0%, 100% {
      opacity: 0.4;
      transform: scaleY(1);
    }

    50% {
      opacity: 0.8;
      transform: scaleY(1.02);
    }
  }

  /* Animacin sutil de resplandor para elementos activos */
  @keyframes mobile-neon-glow {
    0%, 100% {
      box-shadow:
        0 0 10px var(--mobile-neon-glow-soft),
        0 4px 15px rgb(0 0 0 / 30%);
    }

    50% {
      box-shadow:
        0 0 15px var(--mobile-neon-glow),
        0 6px 20px rgb(0 0 0 / 40%);
    }
  }

  /* ===== APLICAR ANIMACIN SUTIL A ELEMENTOS ACTIVOS ===== */
  .nav__toggle.active,
  .nav__language-option.active,
  .nav__link.active {
    animation: mobile-neon-glow 4s ease-in-out infinite !important;
  }

  /* ===== OPTIMIZACIONES DE RENDIMIENTO ===== */
  .nav__menu,
  .nav__toggle,
  .nav__close,
  .nav__language-mobile,
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    transform: translateZ(0) !important;
    will-change: transform, box-shadow !important;
    backface-visibility: hidden !important;
  }

  /* ===== ESTADOS DE FOCUS PARA ACCESIBILIDAD ===== */
  .nav__toggle:focus,
  .nav__close:focus,
  .nav__language-option:focus,
  .nav__link:focus,
  .btn--mobile:focus {
    outline: 2px solid var(--mobile-neon-red-bright) !important;
    outline-offset: 2px !important;
    box-shadow:
      0 0 0 4px rgb(255 45 85 / 30%),
      0 0 20px var(--mobile-neon-glow) !important;
  }

  /* ===== CORRECCIN PARA PANTALLAS MUY PEQUEAS ===== */
  @media screen and (width <= 480px) {
    .nav__toggle,
    .nav__close {
      box-shadow:
        0 3px 12px rgb(0 0 0 / 30%),
        0 0 8px var(--mobile-neon-glow-soft) !important;
    }

    .btn--mobile {
      box-shadow:
        0 6px 20px rgb(255 45 85 / 30%),
        0 0 15px var(--mobile-neon-glow-soft) !important;
    }

    .nav__link,
    .nav__language-option {
      box-shadow:
        0 2px 6px rgb(0 0 0 / 20%),
        inset 0 1px 0 rgb(255 255 255 / 5%) !important;
    }
  }

}

.nav__cta {
  position: relative;
  z-index: calc(var(--navbar-z-index) + 1);
  display: flex;
  flex-direction: row;
  gap: var(--space-sm);
  align-items: center;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
  pointer-events: auto;
}

@media (width >= 1024px) {
  .nav__cta {
    position: static;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: flex-end;
    min-width: auto;
    max-width: 250px;
    overflow: visible;
  }

}

/* ===== CORRECCIN DE TRANSICIONES LENTAS - STARFLEX ===== */

/* Redefinir variables de transicin para mayor velocidad */
:root {
  /* Transiciones ms rpidas y naturales */
  --transition: 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --transition-fast: 0.15s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --transition-slow: 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  /* Transicin especfica para navbar */
  --navbar-transition: 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  /* Transicin para botones */
  --button-transition: 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* ===== CORRECCIONES ESPECFICAS PARA NAVBAR ===== */

/* Enlaces de navegacin - transicin ms rpida */
.nav__link {
  transition: all var(--navbar-transition) !important;
}

.nav__link::before {
  transition: all var(--navbar-transition) !important;
}

.nav__link::after {
  transition: all var(--navbar-transition) !important;
}

/* Botn hamburguesa */
.nav__toggle {
  transition: all var(--navbar-transition) !important;
}

.nav__toggle-line {
  transition: all var(--navbar-transition) !important;
}

/* Men mvil */
.nav__menu {
  transition: left var(--navbar-transition) !important;
}

/* ===== CORRECCIONES PARA BOTONES ===== */

/* Botones principales */
.btn--primary,
.btn--cta,
.btn--cta-video,
.btn--footer,
.btn--secondary {
  transition: all var(--button-transition) !important;
}

/* Efectos de brillo interno de botones */
.btn--primary::before,
.btn--cta::before,
.btn--cta-video::before,
.btn--footer::before {
  transition: left 0.8s ease !important;
}

/* Anillo exterior nen de botones */
.btn--primary::after,
.btn--cta::after,
.btn--cta-video::after,
.btn--footer::after {
  transition: opacity var(--button-transition) !important;
}

/* ===== CORRECCIONES PARA BOTN FLOTANTE ===== */

/* Botn principal flotante */
.floating-widget__main-btn {
  transition: all var(--button-transition) !important;
}

.floating-widget__main-btn::before {
  transition: opacity 0.6s ease !important;
}

.floating-widget__main-icon {
  transition: all var(--button-transition) !important;
}

/* Men flotante */
.floating-widget__menu {
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
}

.floating-widget__menu-item {
  transition: all var(--button-transition) !important;
}

.floating-widget__menu-item::before {
  transition: left 0.8s ease !important;
}

/* ===== CORRECCIONES PARA SELECTOR DE IDIOMA ===== */

.language-switcher__btn {
  transition: all var(--navbar-transition) !important;
}

.language-switcher__dropdown {
  transition: all var(--navbar-transition) !important;
}

.language-switcher__option {
  transition: all var(--navbar-transition) !important;
}

/* ===== CORRECCIONES PARA ELEMENTOS INTERACTIVOS ===== */

/* Canales de contacto */
.contact__channel {
  transition: all var(--button-transition) !important;
}

.contact__channel-icon {
  transition: all var(--button-transition) !important;
}

.contact__channel-btn {
  transition: all var(--button-transition) !important;
}

/* FAQ */
.faq__item {
  transition: all var(--button-transition) !important;
}

.faq__question {
  transition: all var(--button-transition) !important;
}

.faq__icon {
  transition: all var(--button-transition) !important;
}

.faq__icon::before,
.faq__icon::after {
  transition: all var(--button-transition) !important;
}

/* ===== CORRECCIONES PARA CARACTERSTICAS ===== */

.feature__phone::before {
  transition: opacity var(--button-transition) !important;
}

/* ===== CORRECCIONES PARA VIDEOS ===== */

.videos__player {
  transition: all var(--button-transition) !important;
}

.videos__play-overlay {
  transition: all var(--button-transition) !important;
}

.videos__play-button {
  transition: all var(--button-transition) !important;
}

/* ===== CORRECCIONES PARA ELEMENTOS CON HOVER ===== */

/* Asegurar que todos los elementos con hover tengan transiciones rpidas */
*:hover {
  transition-duration: 0.2s !important;
}

/* ===== CORRECCIONES ESPECFICAS PARA MVIL ===== */

@media (width <= 1023px) {
  /* Usar transiciones an ms rpidas en mvil */
  :root {
    --transition-mobile: 0.15s ease !important;
  }

  /* Navbar mvil */
  .nav__toggle,
  .nav__close,
  .nav__link,
  .nav__language-option,
  .btn--mobile {
    transition: all var(--transition-mobile) !important;
  }

  /* Men drawer */
  .nav__menu {
    transition: left 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  }
}

/* ===== OPTIMIZACIN PARA DISPOSITIVOS DE BAJO RENDIMIENTO ===== */

@media (prefers-reduced-motion: reduce) {
  /* Para usuarios que prefieren movimiento reducido, hacer transiciones an ms rpidas */
  :root {
    --transition: 0.1s ease !important;
    --transition-fast: 0.05s ease !important;
    --transition-slow: 0.15s ease !important;
    --navbar-transition: 0.1s ease !important;
    --button-transition: 0.1s ease !important;
  }
}

/* ===== CORRECCIN PARA EFECTOS DE BRILLO QUE NO NECESITAN SER TAN LENTOS ===== */

/* Efectos de shimmer y brillo ms rpidos */
.btn--primary::before,
.btn--cta::before,
.btn--cta-video::before,
.btn--footer::before,
.floating-widget__menu-item::before {
  transition: left 0.6s ease !important;
}

/* Pulsos y animaciones de resplandor ms rpidos */
@keyframes pulse-gentle {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}

/* Reducir duracin de animaciones de resplandor */
.hero__badge::before,
.videos__badge-icon,
.hero__trust::before,
.feature__list-item::before {
  animation: pulse-gentle 2s infinite !important;
}

/* ===== NOTA IMPORTANTE ===== */

/*
Esta correccin acelera las transiciones que estaban configuradas como:
- --transition: 0.6s (600ms)  0.25s (250ms)
- --transition-slow: 1s (1000ms)  0.4s (400ms)
- --transition-fast: 0.3s (300ms)  0.15s (150ms)

Esto hace que las interacciones se sientan mucho ms responsivas y naturales,
especialmente en el navbar y los botones.
*/
body {
  overflow: hidden auto !important;
}

/* ===== CORRECCIN PARA ELEMENTOS CON POSITION ABSOLUTE QUE PUEDAN ESTAR FUERA DEL VIEWPORT ===== */
.contact::before,
.contact__bg-effects,
.contact__orb {
  position: absolute !important;
  padding: 0 !important;
  margin: 0 !important;
}

/* ===== ELIMINACIN COMPLETA DEL CONTENEDOR DEL FOOTER ===== */

