  position: relative !important;
  z-index: 3 !important;
  padding: var(--space-3xl) 0 var(--space-2xl) 0 !important;
  margin: 0 !important;
  margin-bottom: 0 !important;
  overflow: visible !important;

  /* ELIMINAR COMPLETAMENTE EL CONTENEDOR */
  background: none !important;
  backdrop-filter: none !important;
  backdrop-filter: none !important;
  border: none !important;
  border-top: none !important;
  box-shadow: none !important;
}

/* ===== CONTENIDO DEL FOOTER FLOTANDO SIN CONTENEDOR ===== */
.contact__footer-content {
  display: flex !important;
  gap: var(--space-2xl) !important;
  align-items: center !important;
  justify-content: space-between !important;
  width: 100% !important;
  max-width: var(--max-content-width) !important;
  padding: 0 var(--section-padding-x) !important;
  margin: 0 auto !important;
  overflow: visible !important;

  /* ELIMINAR CUALQUIER FONDO O BORDE */
  background: none !important;
  backdrop-filter: none !important;
  backdrop-filter: none !important;
  border: none !important;
  box-shadow: none !important;
}

/* ===== TEXTO DEL FOOTER FLOTANDO LIBREMENTE ===== */
.contact__footer-text {
  padding: 0 !important;
  margin: 0 !important;

  /* Sin contenedor, solo el texto flotando */
  background: none !important;
  border: none !important;
}

.contact__footer-text h3 {
  margin-bottom: var(--space-xs) !important;
  font-size: 1.1rem !important;
  font-weight: 500 !important;
  color: var(--text-muted) !important;

  /* Texto ms sutil para que flote naturalmente */
  opacity: 0.8 !important;
}

.contact__footer-text p {
  margin: 0 !important;
  font-family: var(--font-mono) !important;
  font-size: 0.95rem !important;
  color: var(--text-muted) !important;

  /* Texto ms sutil */
  opacity: 0.7 !important;
}

/* ===== BOTN CTA DEL FOOTER SIN CONTENEDOR ===== */
.contact__footer-cta {
  padding: 0 !important;
  margin: 0 !important;

  /* Sin contenedor, solo el botn flotando */
  background: none !important;
  border: none !important;
}

/* El botn mantiene su estilo pero sin el contenedor que lo rodea */
.btn--footer {
  /* Posicionamiento libre sin restricciones del contenedor */
  position: relative !important;
  z-index: 5 !important;
  display: flex !important;
  flex-direction: column !important;
  gap: var(--space-xs) !important;
