/* ===== CORRECCIN FINAL PARA ELIMINAR CUALQUIER ESPACIO RESIDUAL ===== */
.contact__footer-content::after,
.contact__footer::after,
.contact::after,
body::after,
html::after {
  display: none !important;
  height: 0 !important;
  padding: 0 !important;
  margin: 0 !important;
  content: none !important;
}

/* ===== OPTIMIZACIN ADICIONAL - INTEGRACIN NATURAL CON EL FONDO ===== */

/* El contenido del footer ahora flota naturalmente sobre el fondo de la seccin de contacto */
.contact {
  /* Asegurar que la seccin de contacto tenga suficiente espacio para el contenido flotante */
  position: relative !important;
  padding-bottom: calc(var(--section-padding-y) + var(--space-4xl)) !important;
}

/* ===== EFECTO SUTIL DE SEPARACIN SIN CONTENEDOR ===== */

/* Agregar una lnea sutil para separar visualmente el contenido sin usar un contenedor */
.contact__footer-content::before {
  position: absolute !important;
  top: calc(-1 * var(--space-2xl)) !important;
  left: 50% !important;
  width: 60px !important;
  height: 1px !important;
  content: '' !important;
  background: linear-gradient(90deg,
  transparent 0%,
  rgb(255 255 255 / 20%) 50%,
  transparent 100%
) !important;
  opacity: 0.6 !important;
  transform: translateX(-50%) !important;
}

/* ===== VERSIN MVIL DE LA LNEA SEPARADORA ===== */
@media screen and (width <= 1023px) {
  .contact__footer-content::before {
    top: calc(-1 * var(--space-xl)) !important;
    width: 40px !important;
    opacity: 0.4 !important;
  }
}

/* ===== CORRECCIN FINAL - ELIMINAR CUALQUIER RASTRO DEL CONTENEDOR ORIGINAL ===== */

/* Asegurar que no queden estilos residuales del contenedor original */
.contact__footer {
  /* Resetear completamente cualquier estilo de contenedor */
  background-color: transparent !important;
  background-image: none !important;
  backdrop-filter: none !important;
  backdrop-filter: none !important;
  border: none !important;
  border-top: none !important;
  border-right: none !important;
  border-bottom: none !important;
  border-left: none !important;
  border-radius: 0 !important;
  outline: none !important;
  box-shadow: none !important;
}

/* ===== NOTA IMPORTANTE ===== */

/*
Con estos cambios:
1. Se elimina completamente el contenedor del footer (.contact__footer)
2. El contenido (texto legal y botn) ahora flota libremente sobre el fondo
3. Se mantiene la funcionalidad y accesibilidad
4. Se aprovecha mejor el espacio moviendo el contenido un poco ms abajo
5. Se agrega una lnea sutil de separacin sin usar un contenedor
6. El diseo se ve ms limpio y moderno
7. Se mantiene la responsividad en mviles
*/

/* ===== SIMPLIFICACIN DE ANIMACIONES - SOLO EFECTO DE APARICIN ===== */

/* ===== ELIMINAR TODAS LAS ANIMACIONES COMPLEJAS ===== */

/* Resetear estados iniciales - sin animaciones complejas */
.feature__phone,
.phone,
.phone__screen,
.phone__content,
.phone__app-image {
  /* Eliminar todas las transiciones complejas */
  transition: none !important;
  transform: none !important;
  animation: none !important;
  will-change: auto !important;
}

/* ===== SOLO EFECTO DE APARICIN COMO LAS LETRAS ===== */

/* Estado inicial - invisible */
.feature .phone__app-image,
.feature .phone,
.feature .feature__phone {
  opacity: 0 !important;
  transition: opacity 0.6s ease, transform 0.6s ease !important;
  transform: translateY(30px) !important;
}

/* Estado final - visible (igual que las letras) */
.feature.in-view .phone__app-image,
.feature.in-view .phone,
.feature.in-view .feature__phone {
  opacity: 1 !important;
  transition: opacity 0.6s ease, transform 0.6s ease !important;
  transform: translateY(0) !important;
}

/* ===== ELIMINAR TODOS LOS EFECTOS HOVER ===== */

@media (width >= 1024px) {
  .feature:hover .phone__app-image,
  .feature:hover .phone,
  .feature:hover .feature__phone {
    filter: none !important;
    opacity: 1 !important;
    transition: none !important;

    /* Sin efectos hover - mantener estado normal */
    transform: translateY(0) !important;
  }
}

/* ===== ELIMINAR EFECTOS AMBIENTALES ===== */

.phone::before,
.feature__phone::before {
  /* Eliminar efectos de brillo ambiental */
  display: none !important;
  opacity: 0 !important;
  transition: none !important;
  animation: none !important;
}

/* ===== ELIMINAR TRANSFORMACIONES 3D ===== */

.feature__phone {
  /* Sin perspectiva 3D */
  transform: none !important;
  perspective: none !important;
}

.phone {
  /* Sin rotaciones 3D */
  transform: none !important;
  perspective: none !important;
}

/* ===== MANTENER SOLO PROPIEDADES BSICAS ===== */

.phone__app-image {
  /* Solo propiedades bsicas de imagen */
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  object-position: center !important;
  filter: brightness(1.02) contrast(1.05) saturate(1.08) !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: cover !important;
  border-radius: var(--phone-border-radius) !important;
}

.phone__screen,
.phone__content {
  /* Contenedores simples sin efectos */
  width: 100% !important;
  height: 100% !important;
  overflow: hidden !important;
  background: transparent !important;
  border-radius: var(--phone-border-radius) !important;
}

.phone {
  /* Telfono simple sin efectos */
  width: 100% !important;
  height: 100% !important;
  background: #000 !important;
  border-radius: var(--phone-border-radius) !important;
  box-shadow:
    0 20px 60px rgb(0 0 0 / 40%),
    0 8px 30px rgb(0 0 0 / 30%),
    0 0 0 1px rgb(255 255 255 / 5%) !important;
}

/* ===== DELAYS PROGRESIVOS COMO LAS LETRAS ===== */

/* Aplicar delays similares a los elementos de texto */
.feature:nth-child(1) .phone__app-image,
.feature:nth-child(1) .phone,
.feature:nth-child(1) .feature__phone {
  transition-delay: 0.1s !important;
}

.feature:nth-child(2) .phone__app-image,
.feature:nth-child(2) .phone,
.feature:nth-child(2) .feature__phone {
  transition-delay: 0.2s !important;
}

.feature:nth-child(3) .phone__app-image,
.feature:nth-child(3) .phone,
.feature:nth-child(3) .feature__phone {
  transition-delay: 0.3s !important;
}

.feature:nth-child(4) .phone__app-image,
.feature:nth-child(4) .phone,
.feature:nth-child(4) .feature__phone {
  transition-delay: 0.4s !important;
}

.feature:nth-child(5) .phone__app-image,
.feature:nth-child(5) .phone,
.feature:nth-child(5) .feature__phone {
  transition-delay: 0.5s !important;
}

.feature:nth-child(6) .phone__app-image,
.feature:nth-child(6) .phone,
.feature:nth-child(6) .feature__phone {
  transition-delay: 0.6s !important;
}

/* ===== OPTIMIZACIN PARA MVIL ===== */

@media (width <= 1023px) {
  /* Transiciones ms rpidas en mvil */
  .feature .phone__app-image,
  .feature .phone,
  .feature .feature__phone {
    transition: opacity 0.4s ease, transform 0.4s ease !important;
  }

  .feature.in-view .phone__app-image,
  .feature.in-view .phone,
  .feature.in-view .feature__phone {
    transition: opacity 0.4s ease, transform 0.4s ease !important;
  }

  /* Delays ms cortos en mvil */
  .feature:nth-child(1) .phone__app-image,
  .feature:nth-child(1) .phone,
  .feature:nth-child(1) .feature__phone {
    transition-delay: 0.05s !important;
  }

  .feature:nth-child(2) .phone__app-image,
  .feature:nth-child(2) .phone,
  .feature:nth-child(2) .feature__phone {
    transition-delay: 0.1s !important;
  }

  .feature:nth-child(3) .phone__app-image,
  .feature:nth-child(3) .phone,
  .feature:nth-child(3) .feature__phone {
    transition-delay: 0.15s !important;
  }

  .feature:nth-child(4) .phone__app-image,
  .feature:nth-child(4) .phone,
  .feature:nth-child(4) .feature__phone {
    transition-delay: 0.2s !important;
  }

  .feature:nth-child(5) .phone__app-image,
  .feature:nth-child(5) .phone,
  .feature:nth-child(5) .feature__phone {
    transition-delay: 0.25s !important;
  }

  .feature:nth-child(6) .phone__app-image,
  .feature:nth-child(6) .phone,
  .feature:nth-child(6) .feature__phone {
    transition-delay: 0.3s !important;
  }
}

/* ===== ELIMINAR ANIMACIONES DE CARGA ===== */

.phone__app-image.loading,
.phone__app-image.loaded,
.phone__app-image.error {
  transition: opacity 0.3s ease !important;

  /* Sin animaciones de estado de carga */
  animation: none !important;
}

.phone__app-image.loading {
  opacity: 0.7 !important;
}

.phone__app-image.loaded {
  opacity: 1 !important;
}

.phone__app-image.error {
  opacity: 0.5 !important;
}

/* ===== CORRECCIN PARA USUARIOS CON MOVIMIENTO REDUCIDO ===== */

@media (prefers-reduced-motion: reduce) {
  .feature .phone__app-image,
  .feature .phone,
  .feature .feature__phone {
    transition: opacity 0.2s ease !important;
    transform: none !important;
  }

  .feature.in-view .phone__app-image,
  .feature.in-view .phone,
  .feature.in-view .feature__phone {
    opacity: 1 !important;
    transition: opacity 0.2s ease !important;
    transform: none !important;
  }

  /* Sin delays para movimiento reducido */
  .feature:nth-child(n) .phone__app-image,
  .feature:nth-child(n) .phone,
  .feature:nth-child(n) .feature__phone {
    transition-delay: 0s !important;
  }
}

/* ===== CORRECCIN PARA DISPOSITIVOS DE BAJO RENDIMIENTO ===== */

.performance-mode .feature .phone__app-image,
.performance-mode .feature .phone,
.performance-mode .feature .feature__phone {
  transition: opacity 0.3s ease !important;
  transform: none !important;
}

.performance-mode .feature.in-view .phone__app-image,
.performance-mode .feature.in-view .phone,
.performance-mode .feature.in-view .feature__phone {
  opacity: 1 !important;
  transition: opacity 0.3s ease !important;
  transform: none !important;
}

/* ===== ASEGURAR ALTA ESPECIFICIDAD ===== */

.features .feature .feature__phone .phone .phone__screen .phone__content .phone__app-image {
  /* Forzar solo efecto de aparicin */
  transition: opacity 0.6s ease, transform 0.6s ease !important;
  animation: none !important;
  will-change: auto !important;
}

.features .feature.in-view .feature__phone .phone .phone__screen .phone__content .phone__app-image {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

/* ===== ELIMINAR CUALQUIER KEYFRAME ANIMATION ===== */

@keyframes feature-image-enter {
  /* Deshabilitar todas las animaciones keyframe */
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}

@keyframes ambient-glow {
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}

@keyframes feature-ambient-glow {
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}

@keyframes floating-orb-1 {
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}

@keyframes floating-orb-2 {
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}

@keyframes floating-orb-3 {
  0%, 100% {
    opacity: inherit !important;
    transform: none !important;
  }
}


/* ===== BOTONES DE DESCARGA CON HOVER SIMPLE Y LIMPIO ===== */
.download-btn {
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  overflow: visible;
  text-decoration: none;
  cursor: pointer;
  filter: drop-shadow(0 8px 25px rgb(0 0 0 / 30%));
  transition: all 0.3s ease !important;
}

/* ===== HOVER SIMPLE - SOLO ELEVACIN Y ESCALA ===== */
.download-btn:hover {
  filter: drop-shadow(0 12px 30px rgb(0 0 0 / 40%)) !important;
  transition: all 0.3s ease !important;
  transform: translateY(-3px) scale(1.05) !important;
}

.download-btn:active {
  transition: all 0.15s ease !important;
  transform: translateY(-1px) scale(1.02) !important;
}

.download-btn__image {
  display: block;
  width: auto;
  max-width: 200px;
  height: 60px;
  object-fit: contain;
  object-position: center;
  image-rendering: optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: high-quality;
  filter: brightness(1.02) contrast(1.05) saturate(1.08);
  transition: none !important;
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* ===== CONFIGURACIN ESPECFICA PARA CADA BOTN ===== */
.download-btn--google .download-btn__image {
  width: auto;
  min-width: 180px;
  max-width: 200px;
