  min-width: 180px;
  max-width: 200px;
  height: 60px;
  background-image: url('../assets/AppleStore.avif');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

/* ===== ELIMINAR CUALQUIER EFECTO DE OVERLAY O CUADRADO ===== */
