.download-btn::after {
  display: none !important;
  content: none !important;
}

/* ===== OPTIMIZACIN PARA MVIL ===== */
@media (width <= 1023px) {
  .download-btn:hover {
    filter: drop-shadow(0 10px 25px rgb(0 0 0 / 30%)) !important;
    transform: translateY(-2px) scale(1.03) !important;
