  /* ===== DRAWER COMPLETAMENTE REDISEADO ===== */
  .nav__menu {
    /* Posicionamiento perfecto */
    position: fixed !important;
    top: 0 !important;
    left: -100% !important;
    z-index: 1000 !important;

    /* Layout perfecto */
    display: flex !important;
    flex-direction: column !important;
    align-items: stretch !important;
    justify-content: flex-start !important;
    width: var(--drawer-width) !important;
    max-width: 85vw !important;
    height: 100vh !important;
    height: 100dvh !important;

    /* Padding con safe areas */
    padding: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px) var(--drawer-padding) max(calc(env(safe-area-inset-bottom, 20px) + 20px), 30px) var(--drawer-padding) !important;

    /* Scroll suave */
    overflow-y: auto !important;
