    rgb(21 25 34 / 96%) 100%
  ) !important;
    backdrop-filter: blur(30px) !important;
    backdrop-filter: blur(30px) !important;

    /* Bordes y sombras elegantes */
    border-right: 2px solid var(--border-accent) !important;
    box-shadow:
      0 0 60px rgb(0 0 0 / 80%),
      inset 2px 0 0 rgb(255 45 85 / 20%),
      inset 0 0 100px rgb(255 45 85 / 5%) !important;
    transition: left var(--transition-smooth) !important;
    -webkit-overflow-scrolling: touch !important;
  }

  .nav__menu.active {
    left: 0 !important;
  }

  /* Overlay mejorado */
  .nav__menu::before {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    z-index: -1 !important;
    display: block !important;
    width: 100vw !important;
    height: 100vh !important;
    height: 100dvh !important;
    pointer-events: none !important;
    visibility: hidden !important;
    content: '' !important;
    background: rgb(0 0 0 / 70%) !important;
    backdrop-filter: blur(8px) !important;
    backdrop-filter: blur(8px) !important;
    opacity: 0 !important;
    transition: all var(--transition-smooth) !important;
  }

  .nav__menu.active::before {
    pointer-events: auto !important;
    visibility: visible !important;
    opacity: 1 !important;
  }

  /* ===== BOTN DE CERRAR ELEGANTE ===== */
  .nav__close {
    position: absolute !important;
    top: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px) !important;
    right: var(--drawer-padding) !important;
    z-index: 1001 !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    width: 44px !important;
    height: 44px !important;
    cursor: pointer !important;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 15%) 0%,
    rgb(21 25 34 / 95%) 100%
  ) !important;
    backdrop-filter: blur(20px) !important;
    backdrop-filter: blur(20px) !important;
    border: 1px solid var(--border-accent) !important;
    border-radius: var(--radius-full) !important;
    box-shadow:
      var(--shadow-medium),
      var(--shadow-glow) !important;
    transition: all var(--transition-bounce) !important;
  }

  .nav__close:hover,
  .nav__close:active {
    background: linear-gradient(135deg,
    rgb(255 45 85 / 30%) 0%,
    rgb(21 25 34 / 98%) 100%
  ) !important;
    border-color: var(--border-strong) !important;
    box-shadow:
      var(--shadow-strong),
