  /* ===== SELECTOR DE IDIOMA MODERNO ===== */
  .nav__language-mobile {
    position: relative !important;
    width: 100% !important;
    padding: var(--spacing-lg) !important;
    margin-bottom: var(--spacing-xl) !important;
    overflow: hidden !important;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 10%) 0%,
    var(--mobile-glass) 20%,
    rgb(30 35 48 / 80%) 100%
  ) !important;
    backdrop-filter: blur(25px) !important;
    backdrop-filter: blur(25px) !important;
    border: 1px solid var(--border-accent) !important;
    border-radius: var(--radius-xl) !important;
    box-shadow:
      var(--shadow-medium),
      var(--shadow-glow),
      inset 0 1px 0 rgb(255 255 255 / 10%) !important;
  }

  .nav__language-title {
    margin-bottom: var(--spacing-md) !important;
    font-size: 0.85rem !important;
    font-weight: 700 !important;
    color: var(--starflex-red) !important;
    text-align: center !important;
    text-shadow: 0 0 10px var(--starflex-glow) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;
  }

  .nav__language-options {
    display: flex !important;
    gap: var(--spacing-sm) !important;
  }

  .nav__language-option {
    position: relative !important;
    display: flex !important;
    flex: 1 !important;
    gap: var(--spacing-xs) !important;
    align-items: center !important;
    justify-content: center !important;
    min-height: 40px !important;
    padding: var(--spacing-sm) var(--spacing-md) !important;
    overflow: hidden !important;
    font-size: 0.8rem !important;
    font-weight: 600 !important;
    color: rgb(255 255 255 / 90%) !important;
