  }

  .nav__language-option::before {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent 0%,
    rgb(255 45 85 / 15%) 50%,
    transparent 100%
  ) !important;
    opacity: 1 !important;
    transition: left 0.8s ease !important;
  }

  .nav__language-option:hover::before,
  .nav__language-option:active::before {
    left: 100% !important;
  }

  .nav__language-option:hover,
  .nav__language-option:active {
    color: #fff !important;
    background: rgb(255 45 85 / 15%) !important;
    border-color: var(--border-accent) !important;
    box-shadow:
      var(--shadow-medium),
      var(--shadow-glow) !important;
    transform: translateY(-2px) scale(1.05) !important;
  }

  .nav__language-option.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 10px var(--starflex-glow) !important;
    background: rgb(255 45 85 / 25%) !important;
    border-color: var(--border-strong) !important;
    box-shadow:
      var(--shadow-strong),
      0 0 25px var(--starflex-glow-strong) !important;
  }

  .nav__language-flag {
    font-size: 1.1rem !important;
  }

  .nav__language-name {
    font-weight: 700 !important;
  }

  /* ===== NAVEGACIN PRINCIPAL ELEGANTE ===== */
  .nav__list {
    display: flex !important;
    flex-direction: column !important;
    gap: var(--spacing-sm) !important;
    padding: 0 !important;
    margin: 0 0 var(--spacing-xl) 0 !important;
    list-style: none !important;
  }

  .nav__link {
    position: relative !important;
    display: flex !important;
    align-items: center !important;
    justify-content: flex-start !important;
    min-height: var(--button-height) !important;
    padding: var(--spacing-md) var(--spacing-lg) !important;
    overflow: hidden !important;
    font-size: 0.95rem !important;
    font-weight: 600 !important;
    color: rgb(255 255 255 / 90%) !important;
    text-decoration: none !important;
    text-transform: uppercase !important;
    letter-spacing: 0.5px !important;
    background: var(--mobile-glass) !important;
    backdrop-filter: blur(15px) !important;
    backdrop-filter: blur(15px) !important;
    border: 1px solid var(--border-subtle) !important;
    border-radius: var(--radius-lg) !important;
    box-shadow: var(--shadow-soft) !important;
    transition: all var(--transition-smooth) !important;
