  /* Indicador lateral */
  .nav__link::before {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    display: block !important;
    width: 4px !important;
    height: 100% !important;
    content: '' !important;
    background: var(--starflex-gradient) !important;
    opacity: 0 !important;
    transition: opacity var(--transition-smooth) !important;
  }

  /* Efecto de brillo */
  .nav__link::after {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent 0%,
    rgb(255 45 85 / 10%) 50%,
    transparent 100%
  ) !important;
    opacity: 1 !important;
    transition: left 0.8s ease !important;
  }

  .nav__link:hover::before,
  .nav__link:active::before {
    opacity: 1 !important;
  }

  .nav__link:hover::after,
  .nav__link:active::after {
    left: 100% !important;
  }

  .nav__link:hover,
  .nav__link:active {
    color: #fff !important;
    background: rgb(255 45 85 / 12%) !important;
    border-color: var(--border-accent) !important;
