  .nav__cta-mobile {
    padding-top: var(--spacing-lg) !important;
    margin-top: auto !important;
  }

  .btn--mobile {
    position: relative !important;
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    width: 100% !important;
    min-height: var(--button-height) !important;
    padding: var(--spacing-lg) var(--spacing-xl) !important;
    overflow: hidden !important;
    font-size: 1rem !important;
    font-weight: 700 !important;
    color: #fff !important;
    text-shadow: 0 0 15px rgb(255 255 255 / 80%) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;
    cursor: pointer !important;
    background: var(--starflex-gradient) !important;
    border: 2px solid var(--border-accent) !important;
    border-radius: var(--radius-lg) !important;
    box-shadow:
      var(--shadow-strong),
      0 0 30px var(--starflex-glow-strong) !important;
    transition: all var(--transition-bounce) !important;
  }

  .btn--mobile::before {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent 0%,
    rgb(255 255 255 / 30%) 50%,
    transparent 100%
  ) !important;
    opacity: 1 !important;
    transition: left 1s ease !important;
  }

  .btn--mobile:hover::before,
  .btn--mobile:active::before {
    left: 100% !important;
  }

  .btn--mobile:hover,
  .btn--mobile:active {
    text-shadow: 0 0 20px rgb(255 255 255 / 100%) !important;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 100%) 0%,
    #FF1744 50%,
    rgb(255 45 85 / 100%) 100%
  ) !important;
    border-color: var(--border-strong) !important;
    box-shadow:
      0 20px 60px rgb(255 45 85 / 60%),
      0 0 50px var(--starflex-glow-strong) !important;
    transform: translateY(-4px) scale(1.05) !important;
  }

  /* ===== ANIMACIONES FLUIDAS Y ELEGANTES ===== */
  .nav__menu.active .nav__language-mobile {
    animation: slideInFromLeft 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.1s both;
  }

  .nav__menu.active .nav__link {
    animation: slideInFromLeft 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  }

  .nav__menu.active .nav__link:nth-child(1) { animation-delay: 0.15s; }
  .nav__menu.active .nav__link:nth-child(2) { animation-delay: 0.2s; }
  .nav__menu.active .nav__link:nth-child(3) { animation-delay: 0.25s; }
  .nav__menu.active .nav__link:nth-child(4) { animation-delay: 0.3s; }
  .nav__menu.active .nav__link:nth-child(5) { animation-delay: 0.35s; }

  .nav__menu.active .btn--mobile {
    animation: slideInFromLeft 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s both;
  }

  @keyframes slideInFromLeft {
    from {
      opacity: 0;
      transform: translateX(-40px) scale(0.9);
    }

    to {
      opacity: 1;
      transform: translateX(0) scale(1);
    }
  }

  /* ===== RESPONSIVE PERFECTO ===== */
  @media screen and (width <= 768px) {
    :root {
      --drawer-width: 300px;
      --drawer-padding: 1.25rem;
    }
  }

  @media screen and (width <= 480px) {
    :root {
      --drawer-width: 280px;
      --drawer-padding: 1rem;
      --spacing-lg: 1.25rem;
      --spacing-xl: 1.75rem;
    }

    .nav__language-name {
      display: none !important;
    }

    .nav__link {
      padding: var(--spacing-sm) var(--spacing-md) !important;
      font-size: 0.9rem !important;
    }

    .btn--mobile {
      padding: var(--spacing-md) var(--spacing-lg) !important;
      font-size: 0.9rem !important;
    }
  }

  @media screen and (width <= 360px) {
    :root {
      --drawer-width: 260px;
      --drawer-padding: 0.875rem;
    }

    .nav__close {
      top: max(calc(env(safe-area-inset-top, 15px) + 15px), 25px) !important;
      width: 40px !important;
      height: 40px !important;
    }

    .nav__close svg {
      width: 18px !important;
      height: 18px !important;
    }
  }

  /* ===== SOPORTE PARA DISPOSITIVOS CON NOTCH ===== */
  @supports (padding: max(0px)) {
    .nav__menu {
      padding: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px) max(var(--drawer-padding), env(safe-area-inset-right, var(--drawer-padding))) max(calc(env(safe-area-inset-bottom, 20px) + 20px), 30px) max(var(--drawer-padding), env(safe-area-inset-left, var(--drawer-padding))) !important;
    }

    .nav__close {
      top: max(calc(env(safe-area-inset-top, 20px) + 20px), 30px) !important;
      right: max(var(--drawer-padding), env(safe-area-inset-right, var(--drawer-padding))) !important;
    }
  }

  /* ===== MODO OSCURO MEJORADO ===== */
  @media (prefers-color-scheme: dark) {
    :root {
      --mobile-primary: rgb(5 8 15 / 98%);
      --mobile-secondary: rgb(15 20 30 / 96%);
      --mobile-accent: rgb(25 30 40 / 94%);
    }
  }

  /* ===== ALTO CONTRASTE ===== */
  @media (prefers-contrast: high) {
    .nav__toggle,
    .nav__close,
    .nav__language-option,
    .nav__link,
    .btn--mobile {
      border-width: 2px !important;
    }

    .nav__language-mobile {
      border-width: 2px !important;
    }
  }

  /* ===== MOVIMIENTO REDUCIDO ===== */
  @media (prefers-reduced-motion: reduce) {
    .nav__toggle::before,
    .nav__close::before,
    .nav__language-option::before,
    .nav__link::after,
    .btn--mobile::before {
      display: none !important;
    }

    .nav__menu.active .nav__language-mobile,
    .nav__menu.active .nav__link,
    .nav__menu.active .btn--mobile {
      animation: none !important;
    }

    .nav__toggle,
    .nav__close,
    .nav__language-option,
    .nav__link,
    .btn--mobile {
      transition: all 0.2s ease !important;
    }
  }

  /* ===== OPTIMIZACIONES DE RENDIMIENTO ===== */
  .nav__menu,
  .nav__toggle,
  .nav__close,
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    transform: translateZ(0) !important;
    will-change: transform !important;
    backface-visibility: hidden !important;
    contain: layout style paint !important;
  }

  /* ===== MEJORAS DE ACCESIBILIDAD ===== */
  .nav__toggle:focus,
  .nav__close:focus,
  .nav__language-option:focus,
  .nav__link:focus,
  .btn--mobile:focus {
    outline: 3px solid var(--starflex-red) !important;
    outline-offset: 2px !important;
    box-shadow:
      0 0 0 6px rgb(255 45 85 / 30%),
      var(--shadow-medium) !important;
  }

  /* ===== TOUCH OPTIMIZATIONS ===== */
  .nav__toggle,
  .nav__close,
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    touch-action: manipulation !important;
    -webkit-tap-highlight-color: transparent !important;
    -webkit-touch-callout: none !important;
    user-select: none !important;
  }

  /* ===== PREVENIR SCROLL DEL BODY ===== */
  body.no-scroll {
    position: fixed !important;
    width: 100% !important;
    height: 100% !important;
    overflow: hidden !important;
  }

  /* ===== CORRECCIN FINAL - ELEMENTOS NUNCA CORTADOS ===== */
  .nav__menu {
    /* Espaciado que garantiza visibilidad completa */
    box-sizing: border-box !important;

    /* Asegurar que nunca se corte contenido */
    min-height: 100vh !important;
    min-height: 100dvh !important;
    max-height: 100vh !important;
    max-height: 100dvh !important;
    overflow: hidden auto !important;

    /* Scroll interno si es necesario */
  }

  /* Asegurar que todos los elementos internos sean visibles */
  .nav__language-mobile,
  .nav__list,
  .nav__cta-mobile {
    box-sizing: border-box !important;
    flex-shrink: 0 !important;
    width: 100% !important;
  }

  /* Espaciado inteligente que se adapta a la altura */
  @media screen and (height <= 700px) {
    .nav__menu {
      padding-top: max(calc(env(safe-area-inset-top, 15px) + 15px), 25px) !important;
      padding-bottom: max(calc(env(safe-area-inset-bottom, 15px) + 15px), 25px) !important;
    }

    .nav__language-mobile {
      padding: var(--spacing-md) !important;
      margin-bottom: var(--spacing-md) !important;
    }

    .nav__list {
      gap: var(--spacing-xs) !important;
      margin-bottom: var(--spacing-md) !important;
    }

    .nav__link {
      min-height: 40px !important;
      padding: var(--spacing-sm) var(--spacing-md) !important;
    }

    .btn--mobile {
      min-height: 44px !important;
      padding: var(--spacing-sm) var(--spacing-lg) !important;
    }
  }

  @media screen and (height <= 600px) {
    .nav__menu {
      padding-top: max(calc(env(safe-area-inset-top, 10px) + 10px), 20px) !important;
      padding-bottom: max(calc(env(safe-area-inset-bottom, 10px) + 10px), 20px) !important;
    }

    .nav__language-title {
      margin-bottom: var(--spacing-xs) !important;
      font-size: 0.8rem !important;
    }

    .nav__language-option {
      min-height: 36px !important;
      padding: var(--spacing-xs) var(--spacing-sm) !important;
      font-size: 0.75rem !important;
    }

    .nav__link {
      min-height: 36px !important;
      font-size: 0.85rem !important;
    }

    .btn--mobile {
      min-height: 40px !important;
      font-size: 0.85rem !important;
    }
  }

}

