/* ===== REDISEO COMPLETO DE BOTONES DEL DRAWER MVIL ===== */

/* Botones ultra estticos y modernos para el men mvil */

/* Aplicar solo en dispositivos mviles */
@media screen and (width <= 1023px) {
  /* ===== VARIABLES ESPECFICAS PARA DRAWER ESTTICO ===== */
  :root {
    /* Colores modernos para drawer */
    --drawer-accent: rgb(255 45 85 / 15%);
    --drawer-accent-hover: rgb(255 45 85 / 25%);
    --drawer-accent-active: rgb(255 45 85 / 35%);

    /* Efectos de luz suaves */
    --drawer-glow: 0 0 20px rgb(255 45 85 / 20%);
    --drawer-glow-hover: 0 0 30px rgb(255 45 85 / 40%);

    /* Sombras elegantes */
    --drawer-shadow: 0 4px 20px rgb(0 0 0 / 10%);
    --drawer-shadow-hover: 0 8px 30px rgb(0 0 0 / 20%);

    /* Bordes sutiles */
    --drawer-border: rgb(255 255 255 / 10%);
    --drawer-border-hover: rgb(255 45 85 / 30%);
  }

  /* ===== RESETEAR COMPLETAMENTE LOS BOTONES DEL DRAWER ===== */
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    /* Base limpia */
    position: relative !important;
    overflow: hidden !important;

    /* Eliminar todos los estilos cuadrados anteriores */
    background: none !important;
    backdrop-filter: none !important;
    backdrop-filter: none !important;
    border: none !important;
    box-shadow: none !important;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  }

  /* ===== OPCIONES DE IDIOMA - DISEO PILL MODERNO ===== */
  .nav__language-option {
    /* Layout perfecto */
    display: flex !important;
    gap: 0.5rem !important;
    align-items: center !important;
    justify-content: center !important;
    min-height: 40px !important;
    padding: 0.75rem 1rem !important;
    font-size: 0.8rem !important;
    font-weight: 600 !important;

    /* Tipografa moderna */
    color: rgb(255 255 255 / 90%) !important;
    text-transform: uppercase !important;
    letter-spacing: 0.5px !important;

    /* Diseo pill elegante */
    background: linear-gradient(135deg,
    rgb(255 255 255 / 5%) 0%,
    rgb(255 255 255 / 10%) 100%
  ) !important;
    backdrop-filter: blur(10px) !important;
    backdrop-filter: blur(10px) !important;
    border: 1px solid var(--drawer-border) !important;
    border-radius: 25px !important;

    /* Efectos sutiles */
    box-shadow: var(--drawer-shadow) !important;
  }

  /* Efecto de lnea inferior animada */
  .nav__language-option::before {
    position: absolute !important;
    bottom: 0 !important;
    left: 50% !important;
    display: block !important;
    width: 0 !important;
    height: 2px !important;
    content: '' !important;
    background: linear-gradient(90deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 80%) 100%
  ) !important;
    border-radius: 1px !important;
    opacity: 1 !important;
    transition: all 0.4s ease !important;
    transform: translateX(-50%) !important;
  }

  .nav__language-option:hover,
  .nav__language-option:active {
    color: #fff !important;
    background: linear-gradient(135deg,
    var(--drawer-accent) 0%,
    rgb(255 45 85 / 20%) 100%
  ) !important;
    border-color: var(--drawer-border-hover) !important;
    box-shadow: var(--drawer-shadow-hover), var(--drawer-glow) !important;
    transform: translateY(-2px) !important;
  }

  .nav__language-option:hover::before,
  .nav__language-option:active::before {
    width: 80% !important;
  }

  .nav__language-option.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 10px rgb(255 45 85 / 50%) !important;
    background: linear-gradient(135deg,
    var(--drawer-accent-active) 0%,
    rgb(255 45 85 / 30%) 100%
  ) !important;
    border-color: var(--starflex-red) !important;
    box-shadow: var(--drawer-shadow-hover), var(--drawer-glow-hover) !important;
  }

  .nav__language-option.active::before {
    width: 90% !important;
    box-shadow: 0 0 8px rgb(255 45 85 / 60%) !important;
  }

  /* ===== ENLACES DE NAVEGACIN - DISEO MINIMALISTA ELEGANTE ===== */
  .nav__link {
    /* Efectos sutiles */
    position: relative !important;

    /* Layout perfecto */
    display: flex !important;
    align-items: center !important;
    justify-content: flex-start !important;
    min-height: 48px !important;
    padding: 1rem 1.5rem !important;
    margin: 0.25rem 0 !important;
    font-size: 0.95rem !important;
    font-weight: 500 !important;

    /* Tipografa moderna */
    color: rgb(255 255 255 / 80%) !important;
    text-decoration: none !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;

    /* Diseo minimalista y elegante */
    background: transparent !important;
    border: none !important;
    border-left: 3px solid transparent !important;
    border-radius: 0 15px 15px 0 !important;
  }

  /* Efecto de fondo deslizante */
  .nav__link::before {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    display: block !important;
    width: 0 !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    rgb(255 45 85 / 10%) 0%,
    rgb(255 45 85 / 5%) 100%
  ) !important;
    border-radius: 0 15px 15px 0 !important;
    opacity: 1 !important;
    transition: width 0.4s ease !important;
  }

  /* Icono decorativo */
  .nav__link::after {
    position: absolute !important;
    right: 1rem !important;
    display: block !important;
    font-size: 1rem !important;
    color: rgb(255 45 85 / 60%) !important;
    content: '' !important;
    opacity: 0 !important;
    transition: all 0.3s ease !important;
    transform: translateX(-10px) !important;
  }

  .nav__link:hover,
  .nav__link:active {
    color: #fff !important;
    text-shadow: 0 0 10px rgb(255 255 255 / 30%) !important;
    background: transparent !important;
    border-left-color: var(--starflex-red) !important;
    transform: translateX(8px) !important;
  }

  .nav__link:hover::before,
  .nav__link:active::before {
    width: 100% !important;
  }

  .nav__link:hover::after,
  .nav__link:active::after {
    opacity: 1 !important;
    transform: translateX(0) !important;
  }

  .nav__link.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 15px rgb(255 45 85 / 60%) !important;
    background: transparent !important;
    border-left-color: var(--starflex-red) !important;
    transform: translateX(12px) !important;
  }

  .nav__link.active::before {
    width: 100% !important;
    background: linear-gradient(90deg,
    rgb(255 45 85 / 20%) 0%,
    rgb(255 45 85 / 10%) 100%
  ) !important;
  }

  .nav__link.active::after {
    color: var(--starflex-red) !important;
    opacity: 1 !important;
    transform: translateX(0) !important;
  }

  /* ===== BOTN CTA MVIL - DISEO ESPECTACULAR ===== */
  .btn--mobile {
    /* Animacin sutil */
    position: relative !important;

    /* Layout perfecto */
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    width: 100% !important;
    min-height: 52px !important;
    padding: 1rem 2rem !important;
    margin-top: 1rem !important;
    overflow: hidden !important;
    font-size: 1rem !important;
    font-weight: 700 !important;

    /* Tipografa impactante */
    color: #fff !important;
    text-shadow: 0 0 10px rgb(255 255 255 / 50%) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;

    /* Diseo moderno y atractivo */
    background: linear-gradient(135deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 90%) 50%,
    var(--starflex-red) 100%
  ) !important;
    border: none !important;
    border-radius: 20px !important;

    /* Efectos espectaculares */
    box-shadow:
      0 10px 30px rgb(255 45 85 / 40%),
      0 0 20px rgb(255 45 85 / 30%),
      inset 0 1px 0 rgb(255 255 255 / 20%) !important;
  }

  /* Efecto de brillo deslizante */
  .btn--mobile::before {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent 0%,
    rgb(255 255 255 / 30%) 50%,
    transparent 100%
  ) !important;
    opacity: 1 !important;
    transition: left 1s ease !important;
  }

  /* Pulso sutil de resplandor */
  .btn--mobile::after {
    position: absolute !important;
    inset: -2px !important;
    z-index: -1 !important;
    display: block !important;
    content: '' !important;
    background: linear-gradient(135deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 80%) 100%
  ) !important;
    filter: blur(4px) !important;
    border-radius: 22px !important;
    opacity: 0 !important;
    transition: opacity 0.3s ease !important;
  }

  .btn--mobile:hover,
  .btn--mobile:active {
    text-shadow: 0 0 15px rgb(255 255 255 / 80%) !important;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 100%) 0%,
    #FF1744 50%,
    rgb(255 45 85 / 100%) 100%
  ) !important;
    box-shadow:
      0 15px 40px rgb(255 45 85 / 60%),
      0 0 30px rgb(255 45 85 / 50%),
      inset 0 1px 0 rgb(255 255 255 / 30%) !important;
    transform: translateY(-3px) scale(1.02) !important;
  }

  .btn--mobile:hover::before,
  .btn--mobile:active::before {
    left: 100% !important;
  }

  .btn--mobile:hover::after,
  .btn--mobile:active::after {
    opacity: 1 !important;
  }

  /* ===== CONTENEDOR DEL SELECTOR DE IDIOMA - SIN CUADRADOS ===== */
  .nav__language-mobile {
    /* Sin efectos de brillo molestos */
    position: relative !important;
    padding: 1.5rem !important;
    margin-bottom: 2rem !important;
    overflow: hidden !important;

    /* Diseo limpio y moderno */
    background: linear-gradient(135deg,
    rgb(255 255 255 / 3%) 0%,
    rgb(255 255 255 / 8%) 50%,
    rgb(255 255 255 / 3%) 100%
  ) !important;

    /* Efectos sutiles */
    backdrop-filter: blur(15px) !important;
    backdrop-filter: blur(15px) !important;
    border: 1px solid rgb(255 255 255 / 10%) !important;
    border-radius: 20px !important;
    box-shadow:
      0 8px 25px rgb(0 0 0 / 10%),
      inset 0 1px 0 rgb(255 255 255 / 10%) !important;
  }

  .nav__language-mobile::before {
    display: none !important;
  }

  .nav__language-title {
    margin-bottom: 1rem !important;
    font-size: 0.8rem !important;
    font-weight: 600 !important;
    color: var(--starflex-red) !important;
    text-align: center !important;
    text-shadow: 0 0 8px rgb(255 45 85 / 30%) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;
  }

  .nav__language-options {
    display: flex !important;
    gap: 0.75rem !important;
  }

  /* ===== ANIMACIONES SUAVES PARA ELEMENTOS ===== */
  .nav__menu.active .nav__language-mobile {
    animation: slideInFromLeft 0.5s ease-out 0.1s both;
  }

  .nav__menu.active .nav__link {
    animation: slideInFromLeft 0.5s ease-out both;
  }

  .nav__menu.active .nav__link:nth-child(1) { animation-delay: 0.15s; }
  .nav__menu.active .nav__link:nth-child(2) { animation-delay: 0.2s; }
  .nav__menu.active .nav__link:nth-child(3) { animation-delay: 0.25s; }
  .nav__menu.active .nav__link:nth-child(4) { animation-delay: 0.3s; }
  .nav__menu.active .nav__link:nth-child(5) { animation-delay: 0.35s; }

  .nav__menu.active .btn--mobile {
    animation: slideInFromLeft 0.5s ease-out 0.4s both;
  }

  @keyframes slideInFromLeft {
    from {
      opacity: 0;
      transform: translateX(-30px);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  /* ===== RESPONSIVE PARA PANTALLAS PEQUEAS ===== */
  @media screen and (width <= 480px) {
    .nav__language-option {
      min-height: 36px !important;
      padding: 0.625rem 0.875rem !important;
      font-size: 0.75rem !important;
    }

    .nav__link {
      min-height: 44px !important;
      padding: 0.875rem 1.25rem !important;
      font-size: 0.9rem !important;
    }

    .btn--mobile {
      min-height: 48px !important;
      padding: 0.875rem 1.5rem !important;
      font-size: 0.9rem !important;
    }

    .nav__language-name {
      display: none !important;
    }
  }

  @media screen and (width <= 360px) {
    .nav__language-option {
      min-height: 32px !important;
      padding: 0.5rem 0.75rem !important;
      font-size: 0.7rem !important;
    }

    .nav__link {
      min-height: 40px !important;
      padding: 0.75rem 1rem !important;
      font-size: 0.85rem !important;
    }

    .btn--mobile {
      min-height: 44px !important;
      padding: 0.75rem 1.25rem !important;
      font-size: 0.85rem !important;
    }
  }

  /* ===== OPTIMIZACIONES DE RENDIMIENTO ===== */
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    transform: translateZ(0) !important;
    will-change: transform !important;
    backface-visibility: hidden !important;
    contain: layout style paint !important;
  }

  /* ===== ACCESIBILIDAD MEJORADA ===== */
  .nav__language-option:focus,
  .nav__link:focus,
  .btn--mobile:focus {
    outline: 2px solid var(--starflex-red) !important;
    outline-offset: 2px !important;
    box-shadow:
      0 0 0 4px rgb(255 45 85 / 30%),
      var(--drawer-shadow-hover) !important;
  }

  /* ===== TOUCH OPTIMIZATIONS ===== */
  .nav__language-option,
  .nav__link,
  .btn--mobile {
    touch-action: manipulation !important;
    -webkit-tap-highlight-color: transparent !important;
    -webkit-touch-callout: none !important;
    user-select: none !important;
  }

  /* ===== MODO OSCURO ===== */
  @media (prefers-color-scheme: dark) {
    .nav__language-mobile {
      background: linear-gradient(135deg,
      rgb(255 255 255 / 2%) 0%,
      rgb(255 255 255 / 6%) 50%,
      rgb(255 255 255 / 2%) 100%
    ) !important;
    }

    .nav__language-option {
      background: linear-gradient(135deg,
      rgb(255 255 255 / 3%) 0%,
      rgb(255 255 255 / 8%) 100%
    ) !important;
    }
  }

  /* ===== MOVIMIENTO REDUCIDO ===== */
  @media (prefers-reduced-motion: reduce) {
    .nav__language-option::before,
    .nav__link::before,
    .nav__link::after,
    .btn--mobile::before,
    .btn--mobile::after {
      transition: none !important;
    }

    .nav__menu.active .nav__language-mobile,
    .nav__menu.active .nav__link,
    .nav__menu.active .btn--mobile {
      animation: none !important;
    }

    .nav__language-option,
    .nav__link,
    .btn--mobile {
      transition: all 0.2s ease !important;
    }
  }

}

/* Aplicar solo en dispositivos mviles */
@media screen and (width <= 1023px) {
