    --drawer-safe-top: max(calc(env(safe-area-inset-top, 20px) + 15px), 25px);
    --drawer-safe-bottom: max(calc(env(safe-area-inset-bottom, 20px) + 15px), 25px);
    --drawer-safe-horizontal: max(calc(env(safe-area-inset-left, 20px) + 15px), 20px);

    /* Espaciado adaptativo segn altura de pantalla */
    --drawer-gap: clamp(0.5rem, 2vh, 1rem);
    --drawer-padding: clamp(0.75rem, 3vh, 1.5rem);
