/* ===== CORRECCIONES CSS PARA EL DRAWER MVIL - PRESERVAR SCROLL ===== */

/* Aplicar solo en dispositivos mviles */
@media screen and (width <= 1023px) {
  /* ===== PREVENIR SCROLL DEL BODY CUANDO EL DRAWER EST ABIERTO ===== */
  body.no-scroll {
    /* Usar position fixed en lugar de overflow hidden para preservar scroll */
    position: fixed !important;
    width: 100% !important;
    overflow-y: scroll !important; /* Mantener scrollbar para evitar saltos */
    -webkit-overflow-scrolling: touch !important;
  }

