  /* ===== VARIABLES PARA DRAWER COMPACTO ===== */
  :root {
    /* Espaciado ms compacto */
    --drawer-compact-padding: clamp(1rem, 3vw, 1.25rem);
    --drawer-compact-gap: clamp(0.75rem, 2vh, 1rem);
    --drawer-element-height: clamp(40px, 7vh, 48px);

    /* Ancho del drawer ms compacto */
    --drawer-width-compact: min(300px, 82vw);
  }

  /* ===== OCULTAR LOGO DEL NAVBAR CUANDO DRAWER EST ACTIVO ===== */
  .nav__brand {
    position: relative !important;
    z-index: calc(var(--navbar-z-index) + 1) !important;

    /* Transicin suave para el logo */
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  }

  .nav__logo {
    /* Transicin suave para el logo */
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: translateZ(0) !important;
    backface-visibility: hidden !important;
  }

  /* CUANDO EL MEN EST ACTIVO, OCULTAR EL LOGO */
  body.no-scroll .nav__brand,
  .nav__menu.active ~ .nav__brand {
    pointer-events: none !important;
    visibility: hidden !important;

    /* Ocultar el logo con animacin suave */
    opacity: 0 !important;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: scale(0.8) translateY(-10px) !important;
  }

  body.no-scroll .nav__logo,
  .nav__menu.active ~ .nav__logo {
    filter: blur(2px) !important;

    /* Efectos adicionales para el logo */
    opacity: 0 !important;
    transform: scale(0.8) translateY(-10px) translateZ(0) !important;
  }

  /* ALTERNATIVA: Usar clase especfica cuando el men est abierto */
  .nav-menu-open .nav__brand {
    pointer-events: none !important;
    visibility: hidden !important;

    /* Ocultar logo cuando hay clase especfica */
    opacity: 0 !important;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: scale(0.8) translateY(-10px) !important;
  }

  .nav-menu-open .nav__logo {
    filter: blur(2px) !important;

    /* Efectos adicionales para el logo */
    opacity: 0 !important;
    transform: scale(0.8) translateY(-10px) translateZ(0) !important;
  }

  /* ===== DRAWER PRINCIPAL MS COMPACTO ===== */
  .nav__menu {
    /* Mantener el resto de estilos */
    position: fixed !important;
    top: 0 !important;
    left: -100% !important;
    z-index: 1000 !important;
    box-sizing: border-box !important;

    /* Layout ms eficiente */
    display: flex !important;
    flex-direction: column !important;
    gap: var(--drawer-compact-gap) !important;
    align-items: stretch !important;
    justify-content: flex-start !important;

    /* Ancho ms compacto */
    width: var(--drawer-width-compact) !important;
    height: 100vh !important;
    height: 100dvh !important;

    /* Padding ms compacto */
    padding: var(--drawer-compact-padding) !important;
    padding-top: max(calc(env(safe-area-inset-top, 15px) + var(--drawer-compact-padding)), calc(var(--drawer-compact-padding) + 10px)) !important;
    padding-bottom: max(calc(env(safe-area-inset-bottom, 15px) + var(--drawer-compact-padding)), var(--drawer-compact-padding)) !important;

    /* Sin scroll, layout perfecto */
    overflow: hidden !important;

    /* Fondo y efectos mantenidos */
    background: linear-gradient(135deg,
    rgb(10 13 20 / 98%) 0%,
    rgb(21 25 34 / 96%) 30%,
    rgb(30 35 48 / 94%) 70%,
    rgb(21 25 34 / 96%) 100%
  ) !important;
    backdrop-filter: blur(25px) !important;
    backdrop-filter: blur(25px) !important;
    border-right: 2px solid rgb(255 45 85 / 30%) !important;
    box-shadow:
      0 0 50px rgb(0 0 0 / 80%),
      inset 2px 0 0 rgb(255 45 85 / 20%) !important;

    /* Transicin ms rpida */
    transition: left 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  }

  .nav__menu.active {
    left: 0 !important;
  }

  /* ===== SELECTOR DE IDIOMA COMO PRIMER ELEMENTO ===== */
  .nav__language-mobile {
    /* Layout compacto */
    position: relative !important;
    box-sizing: border-box !important;
    flex-shrink: 0 !important;

    /* ORDEN: Primer elemento */
    order: 1 !important;

    /* Primer elemento del drawer */
    width: 100% !important;
    padding: 0 !important;
    margin: 0 0 var(--drawer-compact-gap) 0 !important;
    overflow: visible !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin contenedor visible */
    background: none !important;
    backdrop-filter: none !important;
    backdrop-filter: none !important;
    border: none !important;
    border-radius: 0 !important;
    box-shadow: none !important;

    /* Sin efectos */
    transition: none !important;
  }

  .nav__language-title {
    position: relative !important;
    z-index: 1 !important;
    padding: 0 !important;
    margin-bottom: calc(var(--drawer-compact-gap) * 0.75) !important;
    font-size: clamp(0.7rem, 2vw, 0.8rem) !important;
    font-weight: 600 !important;

    /* Ttulo como primer elemento visible */
    color: var(--starflex-red) !important;
    text-align: center !important;
    text-shadow: 0 0 6px rgb(255 45 85 / 30%) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin efectos */
    background: none !important;
    border: none !important;
    transition: none !important;
  }

  .nav__language-options {
    /* Layout compacto */
    position: relative !important;
    z-index: 1 !important;
    box-sizing: border-box !important;

    /* Layout compacto */
    display: flex !important;
    gap: calc(var(--drawer-compact-gap) * 0.75) !important;
    width: 100% !important;
    padding: 0 !important;
    margin: 0 0 var(--drawer-compact-gap) 0 !important;
    overflow: visible !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin contenedor visible */
    background: none !important;
    border: none !important;
    box-shadow: none !important;
    transition: none !important;
  }

  .nav__language-option {
    /* Optimizacin compacta */
    position: relative !important;
    box-sizing: border-box !important;
    display: flex !important;

    /* Botones compactos */
    flex: 1 !important;
    gap: 0.375rem !important;
    align-items: center !important;
    justify-content: center !important;
    min-height: calc(var(--drawer-element-height) * 0.85) !important;

    /* Tamao compacto */
    padding: clamp(0.5rem, 2vh, 0.75rem) clamp(0.625rem, 2.5vw, 0.875rem) !important;
    overflow: hidden !important;
    font-size: clamp(0.7rem, 2vw, 0.8rem) !important;
    font-weight: 600 !important;

    /* Tipografa compacta */
    color: rgb(255 255 255 / 90%) !important;
    text-transform: uppercase !important;
    letter-spacing: 0.5px !important;
    pointer-events: auto !important;
    touch-action: manipulation !important;

    /* Interactividad ms rpida */
    cursor: pointer !important;
    user-select: none !important;

    /* Diseo compacto */
    background: rgb(255 255 255 / 8%) !important;
    backdrop-filter: blur(10px) !important;
    backdrop-filter: blur(10px) !important;
    border: 1px solid rgb(255 255 255 / 15%) !important;
    border-radius: 10px !important;
    box-shadow: 0 3px 10px rgb(0 0 0 / 12%) !important;
    transition: all 0.25s ease !important;
    -webkit-tap-highlight-color: transparent !important;
  }

  /* Efectos compactos para opciones de idioma */
  .nav__language-option::before {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    display: block !important;
    width: 0 !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(180deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 80%) 100%
  ) !important;
    border-radius: 10px 0 0 10px !important;
    opacity: 1 !important;
    transition: width 0.25s ease !important;
  }

  .nav__language-option::after {
    position: absolute !important;
    top: 0 !important;
    left: -100% !important;
    display: block !important;
    width: 100% !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    transparent 0%,
    rgb(255 45 85 / 8%) 50%,
    transparent 100%
  ) !important;
    border-radius: 10px !important;
    opacity: 1 !important;
    transition: left 0.5s ease !important;
  }

  .nav__language-option:hover,
  .nav__language-option:active {
    color: #fff !important;
    background: rgb(255 45 85 / 12%) !important;
    border-color: rgb(255 45 85 / 30%) !important;
    box-shadow:
      0 5px 15px rgb(0 0 0 / 18%),
      0 0 10px rgb(255 45 85 / 25%) !important;
    transform: translateY(-1px) scale(1.01) !important;
  }

  .nav__language-option:hover::before,
  .nav__language-option:active::before {
    width: 2px !important;
  }

  .nav__language-option:hover::after,
  .nav__language-option:active::after {
    left: 100% !important;
  }

  .nav__language-option.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 6px rgb(255 45 85 / 40%) !important;
    background: rgb(255 45 85 / 20%) !important;
    border-color: rgb(255 45 85 / 50%) !important;
    box-shadow:
      0 5px 15px rgb(0 0 0 / 20%),
      0 0 15px rgb(255 45 85 / 35%) !important;
  }

  .nav__language-option.active::before {
    width: 2px !important;
    background: linear-gradient(180deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 100%) 100%
  ) !important;
    box-shadow: 0 0 6px rgb(255 45 85 / 50%) !important;
  }

  .nav__language-flag {
    flex-shrink: 0 !important;
    font-size: clamp(0.8rem, 2.2vw, 0.9rem) !important;
    line-height: 1 !important;
    pointer-events: none !important;
    transition: transform 0.25s ease !important;
  }

  .nav__language-name {
    min-width: 0 !important;
    overflow: hidden !important;
    font-weight: 700 !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
    pointer-events: none !important;
    transition: transform 0.25s ease !important;
  }

  .nav__language-option:hover .nav__language-flag,
  .nav__language-option:hover .nav__language-name {
    transform: scale(1.02) !important;
  }

  .nav__language-option.active .nav__language-flag,
  .nav__language-option.active .nav__language-name {
    transform: scale(1.05) !important;
  }

  /* ===== NAVEGACIN PRINCIPAL COMO SEGUNDO ELEMENTO ===== */
  .nav__list {
    /* Layout compacto */
    display: flex !important;

    /* Ocupar espacio disponible */
    flex: 1 !important;
    flex-direction: column !important;
    gap: calc(var(--drawer-compact-gap) * 0.5) !important;

    /* ORDEN: Segundo elemento */
    order: 2 !important;
    min-height: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    overflow: hidden !important;
    list-style: none !important;
  }

  .nav__link {
    position: relative !important;

    /* Diseo mantenido */
    display: flex !important;
    align-items: center !important;
    justify-content: flex-start !important;
    min-height: var(--drawer-element-height) !important;

    /* Tamao compacto */
    padding: clamp(0.625rem, 2.5vh, 0.875rem) clamp(1rem, 3.5vw, 1.25rem) !important;
    overflow: hidden !important;
    font-size: clamp(0.8rem, 2.2vw, 0.9rem) !important;
    font-weight: 500 !important;

    /* Tipografa compacta */
    color: rgb(255 255 255 / 80%) !important;
    text-decoration: none !important;
    text-transform: uppercase !important;
    letter-spacing: 0.5px !important;
    background: transparent !important;
    border: none !important;
    border-left: 3px solid transparent !important;
    border-radius: 0 12px 12px 0 !important;

    /* Efectos ms rpidos */
    transition: all 0.25s ease !important;
  }

  /* Mantener efectos existentes pero ms rpidos */
  .nav__link::before {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    display: block !important;
    width: 0 !important;
    height: 100% !important;
    content: '' !important;
    background: linear-gradient(90deg,
    rgb(255 45 85 / 10%) 0%,
    rgb(255 45 85 / 5%) 100%
  ) !important;
    border-radius: 0 12px 12px 0 !important;
    opacity: 1 !important;
    transition: width 0.3s ease !important;
  }

  .nav__link::after {
    position: absolute !important;
    right: 0.875rem !important;
    display: block !important;
    font-size: clamp(0.75rem, 2vw, 0.9rem) !important;
    color: rgb(255 45 85 / 60%) !important;
    content: '' !important;
    opacity: 0 !important;
    transition: all 0.25s ease !important;
    transform: translateX(-8px) !important;
  }

  .nav__link:hover,
  .nav__link:active {
    color: #fff !important;
    text-shadow: 0 0 6px rgb(255 255 255 / 30%) !important;
    border-left-color: var(--starflex-red) !important;
    transform: translateX(5px) !important;
  }

  .nav__link:hover::before,
  .nav__link:active::before {
    width: 100% !important;
  }

  .nav__link:hover::after,
  .nav__link:active::after {
    opacity: 1 !important;
    transform: translateX(0) !important;
  }

  .nav__link.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 8px rgb(255 45 85 / 50%) !important;
    border-left-color: var(--starflex-red) !important;
    transform: translateX(6px) !important;
  }

  .nav__link.active::before {
    width: 100% !important;
    background: linear-gradient(90deg,
    rgb(255 45 85 / 20%) 0%,
    rgb(255 45 85 / 10%) 100%
  ) !important;
  }

  .nav__link.active::after {
    color: var(--starflex-red) !important;
    opacity: 1 !important;
    transform: translateX(0) !important;
  }

  /* ===== CTA COMO LTIMO ELEMENTO ===== */
  .nav__cta-mobile {
    flex-shrink: 0 !important;

    /* ORDEN: ltimo elemento */
    order: 3 !important;
    padding: 0 !important;

    /* CTA compacto */
    margin: 0 !important;
  }

  .btn--mobile {
    position: relative !important;

    /* Layout compacto */
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;

    /* Botn compacto */
    width: 100% !important;
    min-height: calc(var(--drawer-element-height) * 1.1) !important;
    padding: clamp(0.625rem, 2.5vh, 0.875rem) clamp(1.25rem, 4vw, 1.75rem) !important;
    overflow: hidden !important;
    font-size: clamp(0.8rem, 2.5vw, 0.95rem) !important;
    font-weight: 700 !important;

    /* Tipografa compacta */
    color: #fff !important;
    text-shadow: 0 0 8px rgb(255 255 255 / 50%) !important;
    text-transform: uppercase !important;
    letter-spacing: 1px !important;

    /* Interactividad ms rpida */
    cursor: pointer !important;

    /* Diseo mantenido */
    background: linear-gradient(135deg,
    var(--starflex-red) 0%,
    rgb(255 45 85 / 90%) 50%,
    var(--starflex-red) 100%
  ) !important;
    border: none !important;
    border-radius: 14px !important;

    /* Efectos ms rpidos */
    box-shadow:
      0 6px 20px rgb(255 45 85 / 40%),
      0 0 15px rgb(255 45 85 / 30%),
      inset 0 1px 0 rgb(255 255 255 / 20%) !important;
    transition: all 0.25s ease !important;
  }

  .btn--mobile:hover,
  .btn--mobile:active {
    text-shadow: 0 0 12px rgb(255 255 255 / 80%) !important;
    background: linear-gradient(135deg,
    rgb(255 45 85 / 100%) 0%,
    #FF1744 50%,
    rgb(255 45 85 / 100%) 100%
  ) !important;
    box-shadow:
      0 8px 25px rgb(255 45 85 / 60%),
      0 0 20px rgb(255 45 85 / 50%),
      inset 0 1px 0 rgb(255 255 255 / 30%) !important;
    transform: translateY(-1px) scale(1.01) !important;
  }

  /* ===== ANIMACIONES COMPACTAS Y RPIDAS ===== */
  .nav__menu.active .nav__language-title {
    animation: drawerElementSlideInCompact 0.3s ease-out 0.05s both;
  }

  .nav__menu.active .nav__language-options {
    animation: drawerElementSlideInCompact 0.3s ease-out 0.08s both;
  }

  .nav__menu.active .nav__link:nth-child(1) { animation-delay: 0.11s; }
  .nav__menu.active .nav__link:nth-child(2) { animation-delay: 0.14s; }
  .nav__menu.active .nav__link:nth-child(3) { animation-delay: 0.17s; }
  .nav__menu.active .nav__link:nth-child(4) { animation-delay: 0.2s; }
  .nav__menu.active .nav__link:nth-child(5) { animation-delay: 0.23s; }

  .nav__menu.active .btn--mobile {
    animation: drawerElementSlideInCompact 0.3s ease-out 0.26s both;
  }

  @keyframes drawerElementSlideInCompact {
    from {
      opacity: 0;
      transform: translateX(-20px);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  /* ===== RESPONSIVE COMPACTO ===== */

  /* Pantallas medianas */
  @media screen and (height <= 700px) and (height >= 600px) {
    :root {
      --drawer-compact-padding: 1rem;
      --drawer-compact-gap: 0.875rem;
    }
  }

  /* Pantallas pequeas */
  @media screen and (height <= 600px) {
    :root {
      --drawer-compact-padding: 0.875rem;
      --drawer-compact-gap: 0.75rem;
      --drawer-element-height: 36px;
    }

    .nav__language-name {
      display: none !important;
    }

    .nav__language-option {
      gap: 0 !important;
      justify-content: center !important;
    }
  }

  /* Pantallas muy pequeas */
  @media screen and (height <= 500px) {
    :root {
      --drawer-compact-padding: 0.75rem;
      --drawer-compact-gap: 0.625rem;
      --drawer-element-height: 32px;
    }
  }

  /* Pantallas estrechas */
  @media screen and (width <= 360px) {
    :root {
      --drawer-width-compact: min(280px, 85vw);
    }

    .nav__language-name {
      display: none !important;
    }

    .nav__language-option {
      gap: 0 !important;
      justify-content: center !important;
    }
  }

  /* ===== ACCESIBILIDAD COMPACTA ===== */
  .nav__language-option:focus {
    outline: 2px solid var(--starflex-red) !important;
    outline-offset: 1px !important;
    box-shadow:
      0 0 0 3px rgb(255 45 85 / 30%),
      0 5px 15px rgb(0 0 0 / 20%) !important;
  }

  /* ===== OPTIMIZACIONES DE RENDIMIENTO ===== */
  .nav__brand,
  .nav__logo,
  .nav__language-option {
    transform: translateZ(0) !important;
    will-change: transform !important;
    backface-visibility: hidden !important;
    contain: layout style paint !important;
  }

  /* ===== TOUCH OPTIMIZATIONS ===== */
  .nav__language-option {
    touch-action: manipulation !important;
    -webkit-tap-highlight-color: transparent !important;
    -webkit-touch-callout: none !important;
    user-select: none !important;
  }

  /* Feedback tctil compacto */
  .nav__language-option:active {
    transform: translateY(0) scale(0.99) !important;
  }

  /* ===== OVERLAY MEJORADO ===== */
  .nav__menu::before {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    z-index: -1 !important;
    display: block !important;
    width: 100vw !important;
    height: 100vh !important;
    height: 100dvh !important;
    pointer-events: none !important;
    visibility: hidden !important;

    /* Overlay para cerrar haciendo clic fuera */
    content: '' !important;
    background: rgb(0 0 0 / 70%) !important;
    backdrop-filter: blur(6px) !important;
    backdrop-filter: blur(6px) !important;
    opacity: 0 !important;
    transition: all 0.25s ease !important;
  }

  .nav__menu.active::before {
    pointer-events: auto !important;
    visibility: visible !important;
    opacity: 1 !important;
  }

}

/* ===== MEJORAS PARA BOTONES DEL SELECTOR DE IDIOMA - PREVENIR CORTES ===== */
@media screen and (width <= 1023px) {
  /* ===== VARIABLES OPTIMIZADAS PARA PREVENIR CORTES ===== */
  :root {
    /* Espaciado optimizado que previene overflow */
    --language-container-padding: clamp(0.75rem, 3vw, 1.25rem);
    --language-button-gap: clamp(0.5rem, 2vw, 0.75rem);
    --language-button-padding-h: clamp(0.625rem, 2.5vw, 1rem);
    --language-button-padding-v: clamp(0.5rem, 2vh, 0.75rem);
    --language-button-height: clamp(36px, 6vh, 44px);
    --language-button-font: clamp(0.7rem, 2.2vw, 0.8rem);

    /* Colores mantenidos */
    --language-text-color: var(--starflex-red);
    --language-option-bg: rgb(255 255 255 / 8%);
    --language-option-border: rgb(255 255 255 / 15%);
    --language-option-hover: rgb(255 45 85 / 15%);
    --language-option-active: rgb(255 45 85 / 25%);
  }

  /* ===== CONTENEDOR PRINCIPAL SIN OVERFLOW ===== */
  .nav__language-mobile {
    /* Layout que previene cortes */
    position: relative !important;
    box-sizing: border-box !important;
    flex-shrink: 0 !important;

    /* Layout que previene overflow */
    width: 100% !important;
    max-width: 100% !important;
    padding: 0 var(--language-container-padding) !important;
    margin: 0 !important;
    overflow: visible !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin contenedor visible */
    background: none !important;
    backdrop-filter: none !important;
    backdrop-filter: none !important;
    border: none !important;
    border-radius: 0 !important;
    box-shadow: none !important;

    /* Sin efectos */
    transition: none !important;
  }

  /* ===== TTULO OPTIMIZADO ===== */
  .nav__language-title {
    position: relative !important;
    z-index: 1 !important;
    padding: 0 !important;
    margin-bottom: var(--language-button-gap) !important;
    font-size: var(--language-button-font) !important;
    font-weight: 600 !important;

    /* Tipografa optimizada */
    color: var(--language-text-color) !important;
    text-align: center !important;
    text-shadow: 0 0 6px rgb(255 45 85 / 30%) !important;
    text-transform: uppercase !important;
    letter-spacing: 0.5px !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin efectos */
    background: none !important;
    border: none !important;
    transition: none !important;
  }

  /* ===== CONTENEDOR DE OPCIONES OPTIMIZADO ===== */
  .nav__language-options {
    /* Layout que previene cortes */
    position: relative !important;
    z-index: 1 !important;
    box-sizing: border-box !important;

    /* Layout que previene overflow */
    display: flex !important;
    gap: var(--language-button-gap) !important;
    width: 100% !important;
    max-width: 100% !important;
    padding: 0 !important;
    margin: 0 0 calc(var(--language-button-gap) * 1.5) 0 !important;
    overflow: visible !important;
    pointer-events: none !important;
    cursor: default !important;

    /* Sin contenedor visible */
    background: none !important;
    border: none !important;
    box-shadow: none !important;
    transition: none !important;
  }

  /* ===== BOTONES OPTIMIZADOS PARA NO CORTARSE ===== */
  .nav__language-option {
    /* Optimizacin que previene cortes */
    position: relative !important;
    box-sizing: border-box !important;
    display: flex !important;

    /* Layout flexible que previene overflow */
    flex: 1 !important;

    /* Flexbox que previene overflow */
    flex-basis: 0 !important;
    flex-grow: 1 !important;
    flex-shrink: 1 !important;
    gap: 0.375rem !important;
    align-items: center !important;
    justify-content: center !important;
    min-width: 0 !important;
    max-width: calc(50% - var(--language-button-gap) / 2) !important;
    min-height: var(--language-button-height) !important;
    max-height: var(--language-button-height) !important;

    /* Dimensiones optimizadas */
    padding: var(--language-button-padding-v) var(--language-button-padding-h) !important;
    overflow: hidden !important;
    font-size: var(--language-button-font) !important;
    font-weight: 600 !important;
    line-height: 1.2 !important;

    /* Tipografa optimizada */
    color: rgb(255 255 255 / 90%) !important;
    text-transform: uppercase !important;
    letter-spacing: 0.3px !important;
    pointer-events: auto !important;
    touch-action: manipulation !important;

    /* Interactividad suave */
    cursor: pointer !important;
    user-select: none !important;

    /* Diseo integrado */
    background: var(--language-option-bg) !important;
    backdrop-filter: blur(10px) !important;
    backdrop-filter: blur(10px) !important;
    border: 1px solid var(--language-option-border) !important;
    border-radius: 10px !important;

    /* Sombras sutiles */
    box-shadow: 0 2px 8px rgb(0 0 0 / 10%) !important;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    -webkit-tap-highlight-color: transparent !important;
  }

  /* ===== CONTENIDO DE LOS BOTONES OPTIMIZADO ===== */
  .nav__language-flag {
    flex-shrink: 0 !important;
    font-size: 0.9rem !important;
    line-height: 1 !important;
    pointer-events: none !important;
    transition: transform 0.3s ease !important;
  }

  .nav__language-name {
    flex-shrink: 1 !important;
    min-width: 0 !important;
    overflow: hidden !important;
    font-weight: 700 !important;
    text-overflow: ellipsis !important;
    white-space: nowrap !important;
    pointer-events: none !important;
    transition: transform 0.3s ease !important;
  }

  /* ===== EFECTOS HOVER OPTIMIZADOS ===== */
  .nav__language-option:hover {
    color: #fff !important;
    background: var(--language-option-hover) !important;
    border-color: rgb(255 45 85 / 30%) !important;
    box-shadow: 0 4px 12px rgb(0 0 0 / 15%) !important;
    transform: translateY(-1px) scale(1.01) !important;
  }

  .nav__language-option:active {
    transform: translateY(0) scale(0.99) !important;
  }

  .nav__language-option.active {
    color: var(--starflex-red) !important;
    text-shadow: 0 0 8px rgb(255 45 85 / 40%) !important;
    background: var(--language-option-active) !important;
    border-color: rgb(255 45 85 / 50%) !important;
    box-shadow:
      0 4px 15px rgb(0 0 0 / 20%),
      0 0 15px rgb(255 45 85 / 30%) !important;
  }

  /* ===== RESPONSIVE ESPECFICO PARA PANTALLAS PEQUEAS ===== */

  /* Pantallas medianas */
  @media screen and (height <= 700px) and (height >= 600px) {
    :root {
      --language-button-height: 40px;
      --language-button-padding-v: 0.5rem;
      --language-button-padding-h: 0.75rem;
      --language-button-font: 0.75rem;
      --language-button-gap: 0.625rem;
    }
  }

  /* Pantallas pequeas */
  @media screen and (height <= 600px) {
    :root {
      --language-container-padding: clamp(0.5rem, 2.5vw, 1rem);
      --language-button-height: 36px;
      --language-button-padding-v: 0.375rem;
      --language-button-padding-h: 0.625rem;
      --language-button-font: 0.7rem;
      --language-button-gap: 0.5rem;
    }

    .nav__language-option {
      gap: 0.25rem !important;
      border-radius: 8px !important;
    }

    .nav__language-flag {
      font-size: 0.8rem !important;
    }
  }

  /* Pantallas muy pequeas */
  @media screen and (height <= 500px) {
    :root {
      --language-container-padding: clamp(0.375rem, 2vw, 0.75rem);
      --language-button-height: 32px;
      --language-button-padding-v: 0.25rem;
      --language-button-padding-h: 0.5rem;
      --language-button-font: 0.65rem;
      --language-button-gap: 0.375rem;
    }

    .nav__language-option {
      gap: 0.125rem !important;
      letter-spacing: 0.2px !important;
      border-radius: 6px !important;
    }

    .nav__language-flag {
      font-size: 0.7rem !important;
    }
  }

  /* ===== OPTIMIZACIN PARA PANTALLAS ANCHAS PERO BAJAS ===== */
  @media screen and (height <= 480px) and (width >= 768px) {
    :root {
      --language-container-padding: clamp(1rem, 4vw, 1.5rem);
      --language-button-gap: 0.75rem;
      --language-button-padding-h: 0.875rem;
    }
  }

}

@media screen and (width <= 1023px) {
  /* CUANDO EL MEN EST ACTIVO, OCULTAR EL LOGO SIN BLUR */
  body.no-scroll .nav__brand,
  .nav__menu.active ~ .nav__brand {
    pointer-events: none !important;
    visibility: hidden !important;

    /* Ocultar el logo con animacin suave SIN difuminado */
    opacity: 0 !important;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: scale(0.8) translateY(-10px) !important;
  }

  body.no-scroll .nav__logo,
  .nav__menu.active ~ .nav__logo {
    /* ELIMINAR el filter: blur(2px) para evitar difuminado */
    filter: none !important;

    /* Efectos adicionales para el logo SIN BLUR */
    opacity: 0 !important;
    transform: scale(0.8) translateY(-10px) translateZ(0) !important;
  }

  /* ALTERNATIVA: Usar clase especfica cuando el men est abierto */
  .nav-menu-open .nav__brand {
    pointer-events: none !important;
    visibility: hidden !important;

    /* Ocultar logo cuando hay clase especfica SIN difuminado */
    opacity: 0 !important;
    transition: all 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: scale(0.8) translateY(-10px) !important;
  }

  .nav-menu-open .nav__logo {
    /* ELIMINAR el filter: blur(2px) para evitar difuminado */
    filter: none !important;

    /* Efectos adicionales para el logo SIN BLUR */
    opacity: 0 !important;
    transform: scale(0.8) translateY(-10px) translateZ(0) !important;
  }

  /* Asegurar que el logo mantenga transiciones suaves */
  .nav__brand {
    position: relative !important;
    z-index: calc(var(--navbar-z-index) + 1) !important;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
  }

  .nav__logo {
    /* Asegurar que no hay filtros por defecto */
    filter: none !important;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
    transform: translateZ(0) !important;
    backface-visibility: hidden !important;
  }
}

/* ===== CORRECCIN DE ESPACIADO EXCESIVO EN SECCIONES ===== */

/* Reducir espaciado excesivo en secciones principales */
.hero,
.features,
.videos,
.faq,
.contact {
  padding-top: clamp(2rem, 8vh, 4rem) !important;
  padding-bottom: clamp(2rem, 8vh, 4rem) !important;
}

/* Optimizar espaciado especfico del hero */
.hero {
  min-height: auto !important;
  padding-top: clamp(1rem, 5vh, 2rem) !important;
  padding-bottom: clamp(1.5rem, 6vh, 3rem) !important;
}

/* Reducir espaciado entre elementos del hero */
.hero__badge {
  margin-bottom: clamp(1rem, 3vh, 1.5rem) !important;
}

.hero__title {
  margin-bottom: clamp(1rem, 3vh, 1.5rem) !important;
}

.hero__company-description {
  padding: clamp(1rem, 3vh, 1.5rem) !important;
  margin-bottom: clamp(1rem, 3vh, 1.5rem) !important;
}

.hero__subtitle {
  margin-bottom: clamp(1rem, 3vh, 1.5rem) !important;
}

.hero__cta-group {
  gap: clamp(0.75rem, 2vh, 1rem) !important;
  margin-bottom: clamp(1rem, 3vh, 1.5rem) !important;
}

/* Optimizar espaciado en features */
.features {
  padding-top: clamp(1.5rem, 6vh, 3rem) !important;
  padding-bottom: clamp(1.5rem, 6vh, 3rem) !important;
}

.features__grid {
  gap: clamp(2rem, 8vh, 4rem) !important;
}

/* Optimizar espaciado en videos */
.videos {
  padding-top: clamp(1.5rem, 6vh, 3rem) !important;
  padding-bottom: clamp(1.5rem, 6vh, 3rem) !important;
}

/* Optimizar espaciado en FAQ */
.faq {
  padding-top: clamp(1.5rem, 6vh, 3rem) !important;
  padding-bottom: clamp(1.5rem, 6vh, 3rem) !important;
}

/* Optimizar espaciado en contact */
.contact {
  padding-top: clamp(1.5rem, 6vh, 3rem) !important;
  padding-bottom: clamp(1rem, 4vh, 2rem) !important;
}

/* Reducir espaciado excesivo en mvil */
@media screen and (width <= 1023px) {
  .hero,
  .features,
  .videos,
  .faq,
  .contact {
    padding-top: clamp(1rem, 4vh, 2rem) !important;
    padding-bottom: clamp(1rem, 4vh, 2rem) !important;
  }

  .hero {
    padding-top: clamp(0.5rem, 2vh, 1rem) !important;
    padding-bottom: clamp(1rem, 3vh, 1.5rem) !important;
  }

  .features__grid {
    gap: clamp(1.5rem, 5vh, 2.5rem) !important;
  }

  /* Reducir espaciado entre elementos del hero en mvil */
  .hero__badge,
  .hero__title,
  .hero__company-description,
  .hero__subtitle,
  .hero__cta-group {
    margin-bottom: clamp(0.75rem, 2vh, 1rem) !important;
  }

  .hero__company-description {
    padding: clamp(0.75rem, 2vh, 1rem) !important;
  }

  .hero__cta-group {
    gap: clamp(0.5rem, 1.5vh, 0.75rem) !important;
  }
}

/* Eliminar espaciado excesivo en pantallas muy pequeas */
@media screen and (width <= 480px) {
  .hero,
  .features,
  .videos,
  .faq,
  .contact {
    padding-top: clamp(0.75rem, 3vh, 1.5rem) !important;
    padding-bottom: clamp(0.75rem, 3vh, 1.5rem) !important;
  }

  .hero {
    padding-top: clamp(0.25rem, 1vh, 0.5rem) !important;
    padding-bottom: clamp(0.75rem, 2vh, 1rem) !important;
  }
}
